import ExpertTab from './experttab/Tab.vue'
import API from '@/api'
import Terms from '@/components/common/terms/Terms'
import simulationScrollY from '@/components/common/scroll/simulation-scroll-y'

export default {
  data () {
    return {
      id: this.$route.query.id,
      expert: {
        expert: {
          data: {
            summary: '',
            created_at: '',
            evaluation_done_count: ''
          }
        },
        expertCategories: {
          data: []
        }
      },
      evaluations: [],
      page: {
        page: 1,
        page_size: 5
      },
      loadMore: {},
      isMore: false,
      evaluationId: null,
      userId: ''
    }
  },
  created () {
    this.loadMoreFuc()
    this.getUser()
    this.getSelf()
  },
  components: {
    ExpertTab,
    Terms,
    simulationScrollY
  },
  mounted () {
    this.$on('tab', function (msg) {
      console.log(msg)
    })
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    // 获取专家信息
    getUser () {
      this.$vux.loading.show({
        text: '加载中...'
      })
      API.getUser(this.id, 'expert,expertCategories')
      .then(res => {
        this.expert = res.data.data
      })
    },
    // 申请弹窗
    applyAppraisal (expert) {
      if (this.$route.query.treasureId) {
        let order = {
          name: expert.realname,
          id: this.$route.query.treasureId,
          expert_user_id: expert.id,
          description: expert.expert.data.title,
          price: expert.expert.data.price
        }
        this.evaluationId = expert.id
        this.$store.commit('orderIncrement', order)
        this.$refs.terms.showConfirm()
      } else {
        this.$router.push({name: 'MyTtreasure'})
      }
    },
    // 专家鉴定记录
    getEvaluations (page) {
      if (this.page.page !== 1) {
        this.isMore = true
      } else {
        this.$vux.loading.show({
          text: '加载中...'
        })
      }
      API.getEvaluations(this.id, page)
      .then(res => {
        this._pageData(res.data)
        this.$nextTick(() => this.$refs.ssy.loadEnd())
        this.isMore = false
      })
    },
    // 初始化数据
    _pageData (data) {
      this.evaluations.push(...data.data)
      this.page.page = data.meta.pagination.current_page + 1
    },
    loadMoreFuc () {
      var that = this
      this.loadMore = {
        distance: 0,
        onLoadMore () {
          that.getEvaluations(that.page)
        }
      }
    },
    changetab (index) {
      if (index === 1) {
        this.getEvaluations()
      }
    },
    // 获取自己的信息
    getSelf () {
      API.getSelf()
      .then(res => {
        this.userId = res.data.data.id
      })
    }
  }
}



