const state = {
  expert: {
    idnumber: '',
    mobile: '',
    email: '',
    cate_value: '',
    cate_key: [],
    category_ids: '',
    title: '',
    summary: '',
    price: ''
  }
}

const mutations = {
  expertIncrement (state, v) {
    state.expert = v
  }
}

export default {
  state,
  mutations
}



