
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'
import V from '@/api/verify.js'

const withdraws = {
  money: '',
  content: '',
  bank: '',
  bank_account: '',
  name: ''
}

export default {
  data () {
    return {
      withdraws: withdraws
    }
  },
  components: {
    XHeader
  },
  methods: {
    postWithdraws () {
      let verify = V.withdraws(this.withdraws)
      if (verify.status) {
        this.$vux.loading.show({
          text: '操作中...'
        })
        API.postWithdraws(this.withdraws)
        .then(res => {
          this.$vux.toast.text('提交申请成功')
          this.withdraws = {
            money: '',
            content: '',
            bank: '',
            bank_account: '',
            name: ''
          }
          this.$router.go(-1)
        })
      } else {
        this.$vux.toast.text(verify.text)
      }
    }
  }
}



