
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import API from '@/api'

export default {
  props: {
    id: Number
  },
  data () {
    return {
      isShow: false,
      order: {}
    }
  },
  directives: {
    TransferDom
  },
  components: {
    Confirm
  },
  methods: {
    // 确认操作，发布流程
    onConfirm () {
      this.order = this.$store.state.pay.order
      this.order.visible = 'all'
      API.postEvaluations(this.order.id, this.order)
      .then(res => {
        this.postOrder(res.data.data.id)
      })
    },
    // 生成订单
    postOrder (id) {
      let obj = {
        evaluation_id: id
      }
      API.postOrders(obj)
      .then(res => {
        let order = JSON.stringify(this.$store.state.pay.order)
        this.$router.push({name: 'Pay', query: {orderId: res.data.data.id, order: order}})
      })
    },
    showConfirm () {
      this.isShow = true
    }
  }
}



