
import ExpertItem from './expertItem/Item'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import MainNav from '@/components/common/Mainnav'
import API from '@/api'
import simulationScrollY from '@/components/common/scroll/simulation-scroll-y'

export default {
  data () {
    return {
      selectTag: 'expert',
      isShow: false,
      defaultExperts: [],
      experts: [],
      cate: [],
      searchStr: '',
      allChecked: true,
      page: {
        page: 1,
        page_size: 20
      },
      loadMore: {},
      isMore: false,
      cateId: '',
      userId: null
    }
  },
  directives: {
    TransferDom
  },
  components: {
    ExpertItem,
    XHeader,
    MainNav,
    Popup,
    simulationScrollY
  },
  created () {
    this.getCate()
    this.getExperts()
    this.loadMoreFuc()
    this.getSelf()
  },
  watch: {
    'searchStr' (to) {
      if (to !== '') {
        this.experts = []
        this.page.page = 1
        this.searchExpert(to, this.page, this.cateId)
      } else {
        this.experts = []
        this.page.page = 1
        this.getExperts(this.page, this.cateId)
      }
    }
  },
  methods: {
    showCate () {
      this.isShow = true
    },
    // 获取专家分类
    getCate () {
      API.getCate('expert')
      .then(res => {
        this.cate = this.defaultChecked(res.data.data)
      })
    },
    // 获取专家列表
    getExperts (page, id) {
      if (this.page.page !== 1) {
        this.isMore = true
      } else {
        this.$vux.loading.show({
          text: '加载中...'
        })
      }
      if (this.searchStr === '') {
        API.getExperts(page, id)
        .then(res => {
          this._pageData(res.data)
          this.$nextTick(() => this.$refs.ssy.loadEnd())
          this.isMore = false
        })
      } else {
        this.searchExpert(this.searchStr, page, id)
      }
    },
    // 搜索专家
    searchExpert (keywords, page, id) {
      if (this.page.page !== 1) {
        this.isMore = true
      } else {
        this.$vux.loading.show({
          text: '加载中...'
        })
      }
      API.searchExpert(keywords, page, id)
      .then(res => {
        this._pageData(res.data)
        this.$nextTick(() => this.$refs.ssy.loadEnd())
        this.isMore = false
      })
    },
    // 选择专家分类-全部
    selectAllCate (cate) {
      this.defaultChecked(this.cate)
      this.allChecked = true
      this.experts = this.defaultExperts
      this.cateId = ''
      if (this.searchStr === '') {
        this.fliterExpert(cate.id)
      } else {
        this.experts = []
        this.page.page = 1
        this.searchExpert(this.searchStr, this.page, cate.id)
      }
    },
    // 选择专家分类
    selectCate (cate) {
      this.defaultChecked(this.cate)
      this.allChecked = false
      cate.checked = true
      this.cateId = cate.id
      if (this.searchStr === '') {
        this.fliterExpert(cate.id)
      } else {
        this.experts = []
        this.page.page = 1
        this.searchExpert(this.searchStr, this.page, cate.id)
      }
    },
    // 添加默认checked为flase
    defaultChecked (cate) {
      if (cate.length > 0) {
        cate.forEach(v => {
          v.checked = false
        })
        return cate
      }
    },
    // 过滤专家分类
    fliterExpert (id) {
      this.experts = []
      this.$vux.loading.show({
        text: '加载中...'
      })
      this.page.page = 1
      this.cateId = id
      API.getExperts(this.page, id)
      .then(res => {
        this._pageData(res.data)
        this.$nextTick(() => this.$refs.ssy.loadEnd())
        this.isMore = false
      })
    },
    // 初始化数据
    _pageData (data) {
      this.experts.push(...data.data)
      this.defaultExperts.push(...data.data)
      this.page.page = data.meta.pagination.current_page + 1
    },
    loadMoreFuc () {
      var that = this
      this.loadMore = {
        distance: 0,
        onLoadMore () {
          that.getExperts(that.page, that.cateId)
        }
      }
    },
    // 获取自己的信息
    getSelf () {
      API.getSelf()
      .then(res => {
        this.userId = res.data.data.id
      })
    }
  }
}



