
import XHeader from 'vux/src/components/x-header/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'

const list = () => ['初级鉴定专家', '中级鉴定专家', '高级鉴定专家', '资深鉴定专家']

export default {
  data () {
    return {
      expert: this.$store.state.expert.expert || {},
      items: list()
    }
  },
  components: {
    XHeader,
    Radio,
    Group
  },
  created () {
  },
  methods: {
    saveCtrl () {
      this.$store.commit('expertIncrement', this.expert)
      this.$router.go(-1)
    }
  }
}



