
import XHeader from 'vux/src/components/x-header/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'


export default {
  data () {
    return {
      eva: this.$store.state.eva.eva || {},
      authenticity: [
        {
          key: '1',
          value: '正品'
        },
        {
          key: '0',
          value: '仿制品'
        }
      ]
    }
  },
  components: {
    XHeader,
    Radio,
    Group
  },
  created () {
  },
  watch: {
    'eva.authenticity' (to) {
      this.$store.commit('evaIncrement', this.eva)
      this.$router.go(-1)
    }
  },
  methods: {
    saveCtrl () {
      this.$store.commit('evaIncrement', this.eva)
      this.$router.go(-1)
    },
    change () {

    }
  }
}



