
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'
import V from '@/api/verify.js'

export default {
  data () {
    return {
      user: {
        mobile: '',
        code: ''
      },
      show2: false,
      isLoading: false,
      isSending: false,
      sendText: '发送验证码'
    }
  },
  created () {
    this.getSelf()
  },
  methods: {
    hide () {
      this.$vux.alert.hide()
    },
    // 下一步操作
    next () {
      let forgetVerify = V.forget(this.user)
      this.isLoading = true

      if (forgetVerify.status) {
        API.forget(this.user)
        .then((res) => {
          this.isLoading = false
          if (res.status === 201) {
            this.$vux.toast.text('验证成功')
            this.$router.push({name: 'EditPassword', params: {mobile: this.user.mobile, code: this.user.code}})
          }
        })
      } else {
        this.isLoading = false
        this.$vux.toast.text(forgetVerify.text)
      }
    },
    // 发送验证码
    sendForgetCode () {
      let phoneVerify = V.phone(this.user)
      if (phoneVerify.status) {
        API.sendForgetCode(this.user)
        .then((res) => {
          if (res.status === 204) {
            this.codeBtnCtrl()
          }
        })
      } else {
        this.$vux.toast.text(phoneVerify.text)
      }
    },
    // 重新获取
    codeBtnCtrl () {
      var time = 60
      this.isSending = true
      let timer = setInterval(() => {
        if (time > 0) {
          this.sendText = time + '重新获取'
          time -= 1
        } else {
          this.isSending = false
          this.sendText = '重新获取'
          clearInterval(timer)
        }
      }, 1000)
    },
    // 获取当前用户信息
    getSelf () {
      this.$vux.loading.show({
        text: '加载中...'
      })
      API.getSelf()
      .then(res => {
        this.user.mobile = res.data.data.mobile
      })
    }
  },
  components: {
    XHeader
  }
}



