
import LoadMore from 'vux/src/components/load-more/index.vue'

import Terms from '@/components/common/terms/Terms'
// import API from '@/api'

export default {
  props: {
    items: {
      type: Array
    }
  },
  data () {
    return {
      evaluationId: null
    }
  },
  components: {
    LoadMore,
    Terms
  },
  computed: {
  },
  methods: {
    comment () {
    },
    praise () {
    },
    // toDetail (data) {
    //   var id = null
    //   this.$router.push({name: 'Detail', query: {id: id}})
    // },
    applyAppraisal (id) {
      let order = this.$store.state.pay.order
      let newOrder = {
        name: order.name,
        id: id,
        expert_user_id: order.expert_user_id,
        description: order.description,
        price: order.price
      }
      this.evaluationId = order.expert_user_id
      this.$store.commit('orderIncrement', newOrder)
      this.$refs.terms.showConfirm()
    }
    // toExpert (data) {
    //   if (data.expert.data.length !== 0) {
    //     this.$router.push({name: 'ExpertDetail', query: {id: data.id}})
    //   }
    // }
  }
}



