
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'
import V from '@/api/verify.js'

export default {
  data () {
    return {
      isLoading: false,
      ticket: {
        name: '',
        mobile: '',
        email: '',
        content: ''
      }
    }
  },
  components: {
    XHeader
  },
  methods: {
    postTickets () {
      let verify = V.ticket(this.ticket)
      if (verify.status) {
        this.$vux.loading.show({
          text: '操作中...'
        })
        API.postTickets(this.ticket)
        .then(res => {
          this.$vux.toast.text('提交成功')
          this.ticket = {
            name: '',
            mobile: '',
            email: '',
            content: ''
          }
          this.$router.go(-1)
        })
      } else {
        this.$vux.toast.text(verify.text)
      }
    }
  }
}



