export default {
  props: {
    comments: {
      type: Array
    }
  },
  data () {
    return {
    }
  },
  methods: {
    reportCtrl (id) {
      this.$router.push({name: 'ReportAdd', query: {id: id}})
    },
    replayCtrl (data) {
      this.$router.push({name: 'CommentReplay', query: {id: data.id, name: data.user.data.realname}})
    }
  },
  components: {
  }
}



