import Vue from 'vue'
import axios from 'axios'
import qs from 'qs'

const Util = {
  host: 'http://auth-treasure-frontend.yousails-project.com/api',
  accept: '',
  token: localStorage.getItem('jwt-token') || ''
}

const request = axios.create({
  baseURL: Util.host,
  headers: {
    Accept: Util.accept,
    Authorization: 'Bearer ' + Util.token
  }
})

request.interceptors.response.use((response) => {
  Vue.$vux.loading.hide()
  return response
}, (error) => {
  Vue.$vux.loading.hide()
  // if (error.response.status === 422) {
  Vue.$vux.toast.text(error.response.data.message)
  // }
  return Promise.reject(error)
})

export default {
  /*
   *  登录注册部分
   */
  // 登录
  login (data) {
    return request.post('authorizations', qs.stringify(data))
  },
  // 注册
  signup (data) {
    return request.post('users', qs.stringify(data))
  },
  // 注册发送验证码
  sendCode (data) {
    return request.post('smses/verify', qs.stringify(data))
  },
  // 忘记密码发送验证码
  sendForgetCode (data) {
    return request.post('user/verifyForgetPwd', qs.stringify(data))
  },
  // 忘记密码手机短信码验证
  forget (data) {
    return request.put('user/checkPwd', qs.stringify(data))
  },
  // 忘记密码手机短信码验证
  setPassword (data) {
    return request.put('user/forgetPwd', qs.stringify(data))
  },
  // 退出登录
  logout () {
    return request.delete('authorizations/current')
  },
  // 获取geetest验证
  geetestConfig (data) {
    return request.get('geetest/config' + '?user_id=' + data.user_id + '&client_type=' + data.client_type + '&ip_address=' + data.ip_address)
  },
  /*
   *  个人中心
   */
  // 当前用户信息
  getSelf (include = 'expert,expertCategories') {
    return request.get('user?include=' + include)
  },
  // 上传头像
  uploadAvatar (data) {
    return request.post('user/avatar', data)
  },
  // 更新个人信息
  updateUser (data) {
    return request.put('user', qs.stringify(data))
  },
  // 客户服务
  postTickets (data) {
    return request.post('tickets', qs.stringify(data))
  },
  // 消息列表
  getNotifications (page = {page: 1, page_size: 10}) {
    return request.get('notifications?page=' + page.page + '&page_size=' + page.page_size + '&include=user')
  },
  delNotifications (ids) {
    return request.delete('notifications/' + ids)
  },
  // 消息详情
  getNotification (id) {
    return request.get('notifications/' + id)
  },
  // 消息改为已读
  readNotification (id) {
    return request.put('notifications/' + id + '/read')
  },
  // 我的鉴定
  // new/paid/accept/done
  getMyEvaluations (status = 'new,paid,accept,done', page = {page: 1, page_size: 5}) {
    return request.get('user/evaluations?page=' + page.page + '&page_size=' + page.page_size + '&include=user,expert,treasure,expert.expert,order,user.expert' + '&status=' + status)
  },
  // 我的宝贝
  getMyTreasure (page = {page: 1, page_size: 5}) {
    return request.get('treasures/users/current?page=' + page.page + '&page_size=' + page.page_size + '&include=user,user.expert')
  },
  // 专家认证申请
  expertCertifications (data) {
    return request.post('expert_certifications', qs.stringify(data))
  },
  // 修改密码
  editPassword (data) {
    return request.put('user/password', qs.stringify(data))
  },
  // 删除我的流程
  delEvaluation (id) {
    return request.delete('evaluations/' + id)
  },
  // 撤回我的流程
  userRevoke (id) {
    return request.put('evaluations/' + id + '/revoke')
  },
  // 专家的余额变化日志
  moneyLogs (page = {page: 1, page_size: 10}) {
    return request.get('user/money_logs?page=' + page.page + '&page_size=' + page.page_size + '&include=user')
  },
  // 提现申请
  postWithdraws (data) {
    return request.post('withdraws', qs.stringify(data))
  },
  // 提现记录
  getWithdraws (data) {
    return request.get('withdraws?include=user')
  },
  // 专家接受
  acceptEvaluations (id) {
    return request.put('evaluations/' + id + '/accept')
  },
  // 专家拒绝
  refuseEvaluations (id) {
    return request.put('evaluations/' + id + '/refuse')
  },
  // 专家鉴定中
  doneEvaluations (id, data) {
    return request.put('evaluations/' + id + '/done', qs.stringify(data))
  },
  /*
   *  首页部分
   */
  // 轮换图
  getBanners (size) {
    return request.get('appconfigs?page_size=' + size + '&module=home&key=banner')
  },
  // 今日推荐 today,最新发布 new
  getTreasures (data, page = {page: 1, page_size: 5}) {
    return request.get('treasures?type=' + data.type + '&page=' + page.page + '&page_size=' + page.page_size + '&include=user,user.expert')
  },
  // 宝贝详情
  getTreasure (id) {
    return request.get('treasures/' + id + '?include=user,images')
  },
  // 获取宝贝指定分类
  getCateTreasures (id = '', page = {page: 1, page_size: 5}) {
    return request.get('treasures?category_id=' + id + '&page=' + page.page + '&page_size=' + page.page_size + '&include=user,user.expert')
  },
  // 获取宝贝分类   type , 为1时是宝贝 6时是专家
  getCate (type) {
    return request.get('categories?type=' + type)
  },
  // 获取指定用户信息, 专家时获取专家信息, 认证信息
  getUser (userId, include = '') {
    return request.get('users/' + userId + '?include=' + include)
  },
  // 搜索宝贝
  searchTreasure (keyword, page = {page: 1, page_size: 5}) {
    return request.get('search?keyword=' + keyword + '&include=user,user.expert&page=' + page.page + '&page_size=' + page.page_size)
  },
  // 搜索专家
  searchExpert (keyword, page = {page: 1, page_size: 10}, id = '') {
    return request.get('search/experts?keyword=' + keyword + '&include=expert,expertCategories&page=' + page.page + '&page_size=' + page.page_size + '&category_id=' + id)
  },
  // 发布鉴定
  postEvaluations (id, data) {
    return request.post('treasures/' + id + '/evaluations', qs.stringify(data))
  },
  // 获取微信分享配置
  wechatConfig (data) {
    return request.get('wechat/share/config?url=' + data.url + '&debug=' + data.debug + '&menus=' + data.menus)
  },
  /*
   *  宝贝部分
   */
  // 添加宝贝
  addTreasure (data) {
    return request.post('treasures', qs.stringify(data))
  },
  // 上传图片
  uploadImage (data) {
    return request.post('images', data)
  },
  /*
   *  评论部分
   */
  // 获取评论列表
  getComments (id, page = {page: 1, page_size: 10}) {
    return request.get('treasures/' + id + '/comments?page=' + page.page + '&page_size=' + page.page_size + '&include=user')
  },
  // 发表评论
  addComment (id, data) {
    return request.post('treasures/' + id + '/comments', qs.stringify(data))
  },
  // 删除评论
  delComment (id) {
    return request.delete('comments/' + id)
  },
  // 发表举报
  addReport (id, data) {
    return request.post('reports', qs.stringify(data))
  },
  // 回复评论
  addReplay (id, data) {
    return request.post('comments/' + id + '/reply', qs.stringify(data))
  },
  // 点赞
  addFavorites (id) {
    return request.post('treasures/' + id + '/favorites')
  },
  // 取消点赞
  delFavorites (id) {
    return request.delete('treasures/' + id + '/favorites')
  },
  // 点赞列表
  getFavorites (page = {page: 1, page_size: 10}) {
    return request.get('favorites?page=' + page.page + '&page_size=' + page.page_size + '&include=user')
  },
  // 点赞状态
  getFavoritesStatus (id) {
    return request.get('favorites/' + id + '/status')
  },
  // 鉴定记录
  getEvaluationsHistory (id, status = 'new,paid,accept,done', include = 'expert.expert,expert.expertCategories', page = {page: 1, page_size: 10}) {
    return request.get('treasures/' + id + '/evaluations?include=' + include + '&status=' + status + '&page=' + page.page + '&page_size=' + page.page_size)
  },
  // 鉴定记录详情
  getEvaluationsHistoryDetail (id, include = 'expert,treasure,user,expert.expert,expert.expertCategories,order,treasure.images') {
    return request.get('evaluations/' + id + '?include=' + include)
  },
  /*
   *  专家部分
   */
  // 获取专家列表
  getExperts (page = {page: 1, page_size: 10}, id = '') {
    return request.get('user/experts?page=' + page.page + '&page_size=' + page.page_size + '&include=expert,expertCategories&category_id=' + id)
  },
  // 获取指定用户流程
  getEvaluations (id, page = {page: 1, page_size: 5}) {
    return request.get('users/' + id + '/evaluations?page=' + page.page + '&page_size=' + page.page_size + '&include=treasure,expert,user')
  },
  // 获取专家收到的指定鉴定记录
  getExpertEvaluation (id) {
    return request.get('evaluations/' + id + '/expert?include=expert,treasure,user,expert.expertCategories,expert.expert')
  },
  // 获取专家的收到的鉴定记录   new/paid/accept/done
  getEvaluationsExpert (page = {page: 1, page_size: 5}, status = 'new,paid,accept,done') {
    return request.get('user/evaluations/expert?status=' + status + '&page=' + page.page + '&page_size=' + page.page_size + '&include=treasure,expert,user,user.expert')
  },
  /*
   *   订单部分
   */
  // 生成订单
  postOrders (data) {
    return request.post('orders', qs.stringify(data))
  },
  // 生成订单
  wxPay (id, type = 'NATIVE', openid = '') {
    let data = {
      trade_type: type,
      openid: openid
    }
    return request.post('orders/' + id + '/pay/wx', qs.stringify(data))
  },
  wxOauth (url, scope = 'snsapi_userinfo') {
    return request.get('wechat/oauth?scope=' + scope + '&redirect_uri=' + url)
  },
  // 获取微信订单支付结果
  getOrder (id) {
    return request.get('orders/' + id)
  }
}



