const state = {
  menu: [],
  activeMenu: [],
  menuIndex: 0,
  childMenu: {}
}

const mutations = {
  menuIncrement (state, v) {
    state.menu = v
  },
  activeMenuIncrement (state, v) {
    state.activeMenu = v
  },
  menuIndexIncrement (state, v) {
    state.menuIndex = v
  },
  childMenuIncrement (state, v) {
    state.childMenu = v
  }
}

export default {
  state,
  mutations
}



