
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'

export default {
  data () {
    return {
      id: this.$route.query.id,
      notification: {}
    }
  },
  components: {
    XHeader
  },
  created () {
    this.getNotification()
  },
  methods: {
    getNotification () {
      API.getNotification(this.id)
      .then(res => {
        this.notification = res.data.data
      })
    }
  }
}



