
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'

export default {
  data () {
    return {
      menu: [],
      activeMenu: []
    }
  },
  created () {
    let menuStore = this.$store.state.cate.menu
    let index = this.$store.state.cate.menuIndex
    if (menuStore.length !== 0) {
      this.menu = menuStore
      this.menu[index].checked = true
      this.activeMenu = this.menu[index]
    } else {
      this._getCate()
    }
  },
  methods: {
    // 选择父分类
    selectCate (m, i) {
      this.menu.forEach((v) => {
        v.checked = false
      })
      m.checked = true
      this.activeMenu = m
      this.$store.commit('menuIndexIncrement', i)
    },
    // 选择子分类
    selectChildCate (m, i) {
      // 选中的子分类
      this.$store.commit('childMenuIncrement', m)
      // 当前父分类下所有的子分类
      this.$store.commit('activeMenuIncrement', this.activeMenu.child)
      this.$router.push({name: 'List', params: {cateId: i}})
    },
    // 查看全部
    selectAllCate () {
      let menu = {
        id: this.activeMenu.id,
        name: this.activeMenu.name
      }
      this.$store.commit('childMenuIncrement', menu)
      this.$store.commit('activeMenuIncrement', this.activeMenu.child)
      this.$router.push({name: 'List', params: {cateAll: 1}})
    },
    // 获取分类数据
    _getCate () {
      this.$vux.loading.show({
        text: '加载中...'
      })
      API.getCate('treasure')
      .then((res) => {
        if (res.status === 200) {
          this.menu = this.filterCate(res.data.data)
          this.menu[0].checked = true
          this.activeMenu = this.menu[0]
          this.$vux.loading.hide()
          this.$store.commit('menuIncrement', this.menu)
        }
      })
    },
    // 处理分类数据
    filterCate (cate) {
      if (cate.length !== 0) {
        cate.forEach(v => {
          v.checked = false
        })
        return cate
      }
    }
  },
  components: {
    XHeader
  }
}



