
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
  data () {
    return {
      price: this.$store.state.publish.get_price || ''
    }
  },
  components: {
    XHeader
  },
  methods: {
    savePrice () {
      this.$store.commit('getPriceIncrement', this.price)
      this.$router.go(-1)
    }
  }
}



