
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'
import V from '@/api/verify.js'

export default {
  data () {
    return {
      user: {
        mobile: this.$route.params.mobile || '',
        code: this.$route.params.code || '',
        password: '',
        password_confirmation: ''
      },
      isLoading: false
    }
  },
  created () {
    console.log(this.$route)
  },
  methods: {
    setPassword () {
      let setPasswordVerify = V.setPassword(this.user)
      this.isLoading = true

      if (setPasswordVerify.status) {
        this.$vux.loading.show({
          text: '操作中...'
        })
        API.setPassword(this.user)
        .then((res) => {
          this.isLoading = false
          if (res.status === 201) {
            this.$vux.toast.text('设置成功')
            this.$router.push({name: 'Signin'})
          }
        })
      } else {
        this.isLoading = false
        this.$vux.toast.text(setPasswordVerify.text)
      }
    }
  },
  components: {
    XHeader
  }
}



