
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'

export default {
  data () {
    return {
      user: this.$store.state.user.userinfo || {}
    }
  },
  components: {
    XHeader
  },
  created () {
    console.log(this.$store.state.user.userinfo)
  },
  methods: {
    saveCtrl () {
      API.updateUser(this.user)
      .then(res => {
        if (res.status === 200) {
          this.$vux.toast.text('修改成功')
          this.$router.go(-1)
        }
      })
    }
  }
}



