
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'
import simulationScrollY from '@/components/common/scroll/simulation-scroll-y'

export default {
  data () {
    return {
      evaluations: [],
      page: {
        page: 1,
        page_size: 5
      },
      loadMore: {},
      isMore: false
    }
  },
  components: {
    XHeader,
    simulationScrollY
  },
  created () {
    this.getEvaluationsExpert()
    this.loadMoreFuc()
  },
  computed: {
    evaluationsOrder () {
      let arrDone = []  // 已经完成，status = 4
      let arr = []  // 未完成，status < 4
      arrDone = this.evaluations.filter(v => {
        return v.id >= 4
      })
      arr = this.evaluations.filter(v => {
        return v.id < 4
      })
      arr = arr.sort((a, b) => {
        return b - a
      })
      arr.push(...arrDone)
      // this.evaluations = []
      return arr
    }
  },
  methods: {
    // 专家鉴定记录
    getEvaluationsExpert (page) {
      if (this.page.page !== 1) {
        this.isMore = true
      } else {
        this.evaluations = []
        this.$vux.loading.show({
          text: '加载中...'
        })
      }
      API.getEvaluationsExpert(page)
      .then(res => {
        this._pageData(res.data)
        this.$nextTick(() => this.$refs.ssy.loadEnd())
        this.isMore = false
      })
    },
    // 鉴定操作
    appraisalCtrl (id) {
      API.acceptEvaluations(id)
      .then(res => {
        this.$vux.toast.text('接受鉴定')
        this.page.page = 1
        this.getEvaluationsExpert(this.page)
      })
    },
    // 拒绝操作
    rejectCtrl (id) {
      API.refuseEvaluations(id)
      .then(res => {
        this.$vux.toast.text('拒绝鉴定')
        this.page.page = 1
        this.getEvaluationsExpert(this.page)
      })
    },
    doAppraisalCtrl (data) {
      this.$router.push({name: 'ApplyInput', query: {id: data.id, treasureId: data.treasure.data.id}})
    },
    // 初始化数据
    _pageData (data) {
      this.evaluations.push(...data.data)
      this.page.page = data.meta.pagination.current_page + 1
    },
    loadMoreFuc () {
      var that = this
      this.loadMore = {
        distance: 0,
        onLoadMore () {
          that.getEvaluationsExpert(that.page)
        }
      }
    },
    toDetail (id) {
      this.$router.push({name: 'Detail', query: {id: id}})
    },
    toExpert (data) {
      if (data.expert.data.length !== 0) {
        this.$router.push({name: 'ExpertDetail', query: {id: data.id}})
      }
    }
  }
}



