const state = {
  eva: {
    content: '',
    academic_name: '',
    years: '',
    evaluate: '',
    historical_value: '',
    authenticity: ''
  }
}

const mutations = {
  evaIncrement (state, v) {
    state.eva = v
  }
}

export default {
  state,
  mutations
}



