
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import API from '@/api'

export default {
  data () {
    return {
      index: Number(this.$route.query.index) || 0,
      id: this.$route.query.id,
      evaluation: {
        expert: {
          data: {
            summary: '',
            created_at: '',
            evaluation_done_count: '',
            realname: '',
            avatar: '',
            expert: {
              data: {
                title: ''
              }
            },
            expertCategories: {
              data: []
            }
          }
        },
        treasure: {
          data: {
            image: '',
            created_at: '',
            describe: ''
          }
        },
        user: {
          data: {
            avatar: '',
            realname: ''
          }
        }
      },
      imagesList: []
    }
  },
  components: {
    XHeader,
    Group,
    Cell
  },
  created () {
    this.getEvaluationsHistoryDetail()
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    pay () {
      let order = {
        name: this.evaluation.expert.data.realname,
        id: this.evaluation.treasure.data.id,
        expert_user_id: this.evaluation.expert.data.id,
        description: this.evaluation.expert.data.expert.data.title,
        price: this.evaluation.expert.data.expert.data.price
      }
      this.$store.commit('orderIncrement', order)
      this.$vux.loading.show({
        text: '加载中...'
      })
      API.getOrder(this.evaluation.order.data.id)
      .then(res => {
        let order = JSON.stringify(this.$store.state.pay.order)
        this.$router.push({name: 'Pay', query: {orderId: this.evaluation.order.data.id, order: order}})
      })
    },
    withdraw () {
      API.userRevoke(this.id)
      .then(res => {
        this.$vux.toast.text('撤回鉴定成功')
      })
    },
    // 获取鉴定详情
    getEvaluationsHistoryDetail () {
      this.$vux.loading.show({
        text: '加载中...'
      })
      API.getEvaluationsHistoryDetail(this.id)
      .then(res => {
        this.evaluation = res.data.data
        this.imagesList = this.filterImageSrc(this.evaluation.treasure.data.images.data)
      })
    },
    filterImageSrc (data) {
      if (data.length !== 0) {
        return data.map(v => {
          v.src = v.url
          v.w = this.getImageWidth(v).width
          v.h = this.getImageWidth(v).height
          return v
        })
      }
    },
    getImageWidth (img) {
      let image = new Image()
      image.src = img.src
      let w = image.width
      let h = image.height
      return {
        width: w,
        height: h
      }
    }
  }
}



