function phone (data) {
  let reg = /^1[345789]\d{9}$/
  if (data.mobile === '') {
    return {
      status: false,
      text: '手机号不能为空'
    }
  } else if (!reg.test(data.mobile)) {
    return {
      status: false,
      text: '手机号格式不正确'
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function login (data) {
  let reg = /^1[345789]\d{9}$/
  if (data.mobile === '') {
    return {
      status: false,
      text: '手机号不能为空'
    }
  } else if (!reg.test(data.mobile)) {
    return {
      status: false,
      text: '手机号格式不正确'
    }
  } else if (data.password === '') {
    return {
      status: false,
      text: '密码不能为空'
    }
  } else if (data.password.length < 6) {
    return {
      status: false,
      text: '密码必须大于6位'
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function signup (data) {
  let reg = /^1[345789]\d{9}$/
  if (data.mobile === '') {
    return {
      status: false,
      text: '手机号不能为空'
    }
  } else if (!reg.test(data.mobile)) {
    return {
      status: false,
      text: '手机号格式不正确'
    }
  } else if (data.password === '') {
    return {
      status: false,
      text: '密码不能为空'
    }
  } else if (data.password.length < 6) {
    return {
      status: false,
      text: '密码必须大于6位'
    }
  } else if (data.code === '') {
    return {
      status: false,
      text: '验证码不能为空'
    }
  } else if (data.code.length !== 4) {
    return {
      status: false,
      text: '验证码长度不对'
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function forget (data) {
  let reg = /^1[345789]\d{9}$/
  if (data.mobile === '') {
    return {
      status: false,
      text: '手机号不能为空'
    }
  } else if (!reg.test(data.mobile)) {
    return {
      status: false,
      text: '手机号格式不正确'
    }
  } else if (data.code === '') {
    return {
      status: false,
      text: '验证码不能为空'
    }
  } else if (data.code.length !== 4) {
    return {
      status: false,
      text: '验证码长度不对'
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function setPassword (data) {
  if (data.password === '') {
    return {
      status: false,
      text: '密码不能为空'
    }
  } else if (data.password.length < 6) {
    return {
      status: false,
      text: '密码必须大于6位'
    }
  } else if (data.password !== data.password_confirmation) {
    return {
      status: false,
      text: '两次输入密码不一致'
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function editPassword (data) {
  if (data.old_password === '') {
    return {
      status: false,
      text: '旧密码不能为空'
    }
  } else if (data.old_password.length < 6) {
    return {
      status: false,
      text: '旧密码必须大于6位'
    }
  } else if (data.password === '') {
    return {
      status: false,
      text: '新密码不能为空'
    }
  } else if (data.password.length < 6) {
    return {
      status: false,
      text: '新密码必须大于6位'
    }
  } else if (data.old_password === data.password) {
    return {
      status: false,
      text: '新旧密码不能相同'
    }
  } else if (data.password !== data.password_confirmation) {
    return {
      status: false,
      text: '两次输入密码不一致'
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function upload (data) {
  if (data.name === '') {
    return {
      status: false,
      text: '宝贝名称不能为空'
    }
  } else if (data.describe === '') {
    return {
      status: false,
      text: '宝贝描述不能为空'
    }
  } else if (data.describe.length > 200) {
    return {
      status: false,
      text: '宝贝描述长度太长'
    }
  } else if (data.images.length === 0) {
    return {
      status: false,
      text: '宝贝图片不能为空'
    }
  } else if (data.category_id === '') {
    return {
      status: false,
      text: '宝贝分类不能为空'
    }
  } else if (data.visible === '') {
    return {
      status: false,
      text: '隐私设置不能为空'
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function expert (data) {
  let regIdnumber = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/

  if (data.realname === '') {
    return {
      status: false,
      text: '请在编辑资料里填写真实姓名'
    }
  } if (data.province === '' || data.city === '' || data.district === '') {
    return {
      status: false,
      text: '请在编辑资料里填写地址'
    }
  } else if (data.category_ids === '') {
    return {
      status: false,
      text: '认证领域不能为空'
    }
  } else if (data.title === '') {
    return {
      status: false,
      text: '认证称号不能为空'
    }
  } else if (data.summary === '') {
    return {
      status: false,
      text: '专家简介不能为空'
    }
  } else if (data.price === '') {
    return {
      status: false,
      text: '鉴定价格不能为空'
    }
  } else if (data.mobile === '') {
    return {
      status: false,
      text: '手机号码不能为空'
    }
  } else if (data.email === '') {
    return {
      status: false,
      text: '邮箱不能为空'
    }
  } else if (data.idnumber === '') {
    return {
      status: false,
      text: '身份证格不能为空'
    }
  } else if (!regIdnumber.test(data.idnumber)) {
    return {
      status: false,
      text: '身份证格式不正确'
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

// 编辑资料，姓名字段验证
function username (name) {
  let reg = /[\u4e00-\u9fa5]/
  let len = 0

  if (reg.test(name)) {
    len = name.length * 2
  } else {
    len = name.length
  }

  if (name === '') {
    return {
      status: false,
      text: '姓名不能为空'
    }
  } else if (len > 10) {
    return {
      status: false,
      text: '姓名长度不能超过10个字符'
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

// 专家认证，价格字段验证
function price (price) {
  if (price === '') {
    return {
      status: false,
      text: '价格不能为空'
    }
  } else if (isNaN(price)) {
    return {
      status: false,
      text: '价格只能为数字'
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

// 客服消息
function ticket (data) {
  let reg = /^1[345789]\d{9}$/
  if (data.content === '') {
    return {
      status: false,
      text: '内容不能为空'
    }
  } else if (data.mobile === '') {
    return {
      status: false,
      text: '手机不能为空'
    }
  } else if (!reg.test(data.mobile)) {
    return {
      status: false,
      text: '手机号格式不正确'
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

// 客服消息
function withdraws (data) {
  if (data.name === '') {
    return {
      status: false,
      text: '姓名不能为空'
    }
  } else if (data.bank === '') {
    return {
      status: false,
      text: '银行名称不能为空'
    }
  } else if (data.bank_account === '') {
    return {
      status: false,
      text: '银行卡号不能为空'
    }
  } else if (data.money === '') {
    return {
      status: false,
      text: '金额不能为空'
    }
  } else if (isNaN(data.money)) {
    return {
      status: false,
      text: '金额格式不正确'
    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

function evaluations (data) {
  if (data.academic_name === '') {
    return {
      status: false,
      text: '学术名称不能为空'
    }
  } else if (data.years === '') {
    return {
      status: false,
      text: '宝贝年代与真伪不能为空'
    }
  } else if (data.evaluate === '') {
    return {
      status: false,
      text: '估价不能为空'
    }
  } else if (data.historical_value === '') {
    return {
      status: false,
      text: '历史价值和艺术价值不能为空'
    }
  } else if (data.historical_value === '') {
    return {
      status: false,
      text: '真伪不能为空'

    }
  } else {
    return {
      status: true,
      text: ''
    }
  }
}

export default {
  phone,
  login,
  signup,
  forget,
  setPassword,
  editPassword,
  upload,
  expert,
  username,
  price,
  ticket,
  withdraws,
  evaluations
}



