import Vue from 'vue'
import Router from 'vue-router'
/* 加载 */
import Loader from '@/components/loader/Loader'
/* 登录 */
import Signin from '@/components/auth/Signin'
import Signup from '@/components/auth/Signup'
import Forget from '@/components/auth/Forget'
import Protocol from '@/components/auth/Protocol'
import Setpassword from '@/components/auth/Setpassword'
/* 主页面 */
import Index from '@/components/main/Index'
import List from '@/components/main/List'
import Detail from '@/components/main/Detail'
import Cate from '@/components/main/Cate'
import Activity from '@/components/main/Activity'
import Search from '@/components/main/Search'
import History from '@/components/main/History'
import HistoryDetail from '@/components/main/HistoryDetail'
import Comment from '@/components/main/Comment'
import CommentAdd from '@/components/main/CommentAdd'
import CommentReplay from '@/components/main/CommentReplay'
import ReportAdd from '@/components/main/ReportAdd'
import OtherPerson from '@/components/main/OtherPerson'
import Build from '@/components/main/Build'
/* 鉴宝 */
import Upload from '@/components/treasure/Upload'
import Collect from '@/components/treasure/Collect'
import Select from '@/components/treasure/Select'
import Pay from '@/components/treasure/Pay'
import TreasureCate from '@/components/treasure/select/Cate'
import Privacy from '@/components/treasure/select/Privacy'
import TreasurePrice from '@/components/treasure/select/Price'
import TreasurePerson from '@/components/treasure/select/Person'
/* 专家 */
import ExpertList from '@/components/expert/List'
import ExpertIndex from '@/components/expert/Index'
import ExpertDetail from '@/components/expert/Detail'
import MyTtreasure from '@/components/expert/MyTtreasure'
/* 个人中心 */
import PersonalIndex from '@/components/personal/Index'
import Edit from '@/components/personal/Edit'
import EditName from '@/components/personal/edit/Name'
import EditSex from '@/components/personal/edit/Sex'
import EditEmail from '@/components/personal/edit/Email'
import EditMobile from '@/components/personal/edit/Mobile'
import Treasure from '@/components/personal/Treasure'
import Appraisal from '@/components/personal/Appraisal'
import AppraisalDetail from '@/components/personal/AppraisalDetail'
import Apply from '@/components/personal/Apply'
import ApplyDetail from '@/components/personal/ApplyDetail'
import ApplyInput from '@/components/personal/ApplyInput'

import ApplyInputAcademicName from '@/components/personal/apply_input/AcademicName'
import ApplyInputYears from '@/components/personal/apply_input/Years'
import ApplyInputEvaluate from '@/components/personal/apply_input/Evaluate'
import ApplyInputHistoricalValue from '@/components/personal/apply_input/HistoricalValue'
import ApplyInputAuthenticity from '@/components/personal/apply_input/Authenticity'
import ApplyInputContent from '@/components/personal/apply_input/Content'

import ApplyPrice from '@/components/personal/apply/Price'
import ApplyIdnumber from '@/components/personal/apply/Idnumber'
import ApplyEmail from '@/components/personal/apply/Email'
import ApplyMobile from '@/components/personal/apply/Mobile'
import ApplySummary from '@/components/personal/apply/Summary'
import ApplyTitle from '@/components/personal/apply/Title'
import ApplyDomain from '@/components/personal/apply/Domain'
import Wallet from '@/components/personal/Wallet'
import WalletDetail from '@/components/personal/WalletDetail'
import Expert from '@/components/personal/Expert'
import Verify from '@/components/personal/Verify'
import EditPassword from '@/components/personal/EditPassword'
import Message from '@/components/personal/Message'
import MessageDetail from '@/components/personal/MessageDetail'
import Service from '@/components/personal/Service'
import About from '@/components/personal/About'

/* 登录判断 */
import Auth from '@/util/auth.js'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    /* 选择登录方式 */
    {
      path: '/',
      name: 'Loader',
      component: Loader,
      meta: {guest: true}
    },
    /* 登录 */
    {
      path: '/auth/signin',
      name: 'Signin',
      component: Signin,
      meta: {guest: true}
    },
    /* 注册 */
    {
      path: '/auth/signup',
      name: 'Signup',
      component: Signup,
      meta: {guest: true}
    },
    {
      path: '/auth/forget',
      name: 'Forget',
      component: Forget,
      meta: {guest: true}
    },
    {
      path: '/auth/signup/protocol',
      name: 'Protocol',
      component: Protocol,
      meta: {guest: true}
    },
    {
      path: '/auth/setpassword',
      name: 'Setpassword',
      component: Setpassword,
      meta: {guest: true}
    },
    /* 首页 */
    {
      path: '/main',
      name: 'Index',
      component: Index
    },
    /* 宝贝列表 */
    {
      path: '/main/list',
      name: 'List',
      component: List
    },
    /* 宝贝详情 */
    {
      path: '/main/detail',
      name: 'Detail',
      component: Detail
    },
    /* 宝贝详情 */
    {
      path: '/cate',
      name: 'Cate',
      component: Cate
    },
    /* 活动／服务跳转页面 */
    {
      path: '/main/activity',
      name: 'Activity',
      component: Activity
    },
    /* 搜索 */
    {
      path: '/main/search',
      name: 'Search',
      component: Search
    },
    /* 鉴定历史 */
    {
      path: '/main/history',
      name: 'History',
      component: History
    },
    /* 鉴定历史详情 */
    {
      path: '/main/history/detail',
      name: 'HistoryDetail',
      component: HistoryDetail
    },
    /* 评论 */
    {
      path: '/main/comment',
      name: 'Comment',
      component: Comment,
      meta: {auth: true}
    },
    /* 评论详情 */
    {
      path: '/main/comment/add',
      name: 'CommentAdd',
      component: CommentAdd
    },
    /* 回复评论 */
    {
      path: '/main/comment/replay',
      name: 'CommentReplay',
      component: CommentReplay
    },
    /* 举报评论 */
    {
      path: '/main/report/add',
      name: 'ReportAdd',
      component: ReportAdd
    },
    /* 其他人的详情页面 */
    {
      path: '/main/other',
      name: 'OtherPerson',
      component: OtherPerson
    },
    /* 建设中的页面 */
    {
      path: '/main/build',
      name: 'Build',
      component: Build
    },
    /*
     * 鉴宝
     */
    /* 上传宝贝 */
    {
      path: '/treasure',
      name: 'Upload',
      component: Upload,
      meta: {auth: true}
    },
    /* 选择宝物分类 */
    {
      path: '/treasure/select/cate',
      name: 'TreasureCate',
      component: TreasureCate,
      meta: {auth: true}
    },
    /* 收藏或传承 */
    {
      path: '/treasure/collect',
      name: 'Collect',
      component: Collect,
      meta: {auth: true}
    },
    /* 指定鉴定师 */
    {
      path: '/treasure/select',
      name: 'Select',
      component: Select,
      meta: {auth: true}
    },
    /* 支付流程 */
    {
      path: '/wxpay/index',
      name: 'Pay',
      component: Pay,
      meta: {auth: true}
    },
    /* 隐私设置 */
    {
      path: '/treasure/select/privacy',
      name: 'Privacy',
      component: Privacy,
      meta: {auth: true}
    },
    /* 原收藏人设置 */
    {
      path: '/treasure/select/person',
      name: 'TreasurePerson',
      component: TreasurePerson,
      meta: {auth: true}
    },
    /* 购买价格 */
    {
      path: '/treasure/select/Price',
      name: 'TreasurePrice',
      component: TreasurePrice,
      meta: {auth: true}
    },
    /*
     * 专家
     */
    /* 专家主页 */
    {
      path: '/expert',
      name: 'ExpertIndex',
      component: ExpertIndex,
      meta: {auth: true}
    },
    /* 专家列表 */
    {
      path: '/expert/list',
      name: 'ExpertList',
      component: ExpertList,
      meta: {auth: true}
    },
    /* 专家详情 */
    {
      path: '/expert/detail',
      name: 'ExpertDetail',
      component: ExpertDetail,
      meta: {auth: true}
    },
    /* 所有鉴定记录 */
    {
      path: '/expert/my/treasure',
      name: 'MyTtreasure',
      component: MyTtreasure,
      meta: {auth: true}
    },
    /*
     * 个人中心
     */
    /* 个人中心 */
    {
      path: '/personal',
      name: 'PersonalIndex',
      component: PersonalIndex,
      meta: {auth: true}
    },
    /* 编辑个人资料 */
    {
      path: '/personal/edit',
      name: 'Edit',
      component: Edit,
      meta: {auth: true}
    },
    /* 编辑个人资料内页 */
    {
      path: '/personal/edit/name',
      name: 'EditName',
      component: EditName,
      meta: {auth: true}
    },
    {
      path: '/personal/edit/sex',
      name: 'EditSex',
      component: EditSex,
      meta: {auth: true}
    },
    {
      path: '/personal/edit/mobile',
      name: 'EditMobile',
      component: EditMobile,
      meta: {auth: true}
    },
    {
      path: '/personal/edit/email',
      name: 'EditEmail',
      component: EditEmail,
      meta: {auth: true}
    },
    /* 我的宝物 */
    {
      path: '/personal/treasure',
      name: 'Treasure',
      component: Treasure,
      meta: {auth: true}
    },
    /* 我的鉴定 */
    {
      path: '/personal/appraisal',
      name: 'Appraisal',
      component: Appraisal,
      meta: {auth: true}
    },
    /* 鉴定操作内页 */
    {
      path: '/personal/appraisal/detail',
      name: 'AppraisalDetail',
      component: AppraisalDetail,
      meta: {auth: true}
    },
    /* 鉴定申请 */
    {
      path: '/personal/apply',
      name: 'Apply',
      component: Apply,
      meta: {auth: true}
    },
    /* 鉴定申请详情 */
    {
      path: '/personal/apply/detail',
      name: 'ApplyDetail',
      component: ApplyDetail,
      meta: {auth: true}
    },
    /* 鉴定专家输入内容 */
    {
      path: '/personal/apply/detail/input',
      name: 'ApplyInput',
      component: ApplyInput,
      meta: {auth: true}
    },
    /* 鉴定专家输入内容 - 二级页面信息填入 */
    {
      path: '/personal/apply/detail/input/name',
      name: 'ApplyInputAcademicName',
      component: ApplyInputAcademicName,
      meta: {auth: true}
    },
    {
      path: '/personal/apply/detail/input/years',
      name: 'ApplyInputYears',
      component: ApplyInputYears,
      meta: {auth: true}
    },
    {
      path: '/personal/apply/detail/input/eva',
      name: 'ApplyInputEvaluate',
      component: ApplyInputEvaluate,
      meta: {auth: true}
    },
    {
      path: '/personal/apply/detail/input/his',
      name: 'ApplyInputHistoricalValue',
      component: ApplyInputHistoricalValue,
      meta: {auth: true}
    },
    {
      path: '/personal/apply/detail/input/auth',
      name: 'ApplyInputAuthenticity',
      component: ApplyInputAuthenticity,
      meta: {auth: true}
    },
    {
      path: '/personal/apply/detail/input/content',
      name: 'ApplyInputContent',
      component: ApplyInputContent,
      meta: {auth: true}
    },
    /* 鉴定申请内页 */
    {
      path: '/personal/apply/name',
      name: 'ApplyPrice',
      component: ApplyPrice,
      meta: {auth: true}
    },
    {
      path: '/personal/apply/idnumber',
      name: 'ApplyIdnumber',
      component: ApplyIdnumber,
      meta: {auth: true}
    },
    {
      path: '/personal/apply/email',
      name: 'ApplyEmail',
      component: ApplyEmail,
      meta: {auth: true}
    },
    {
      path: '/personal/apply/mobile',
      name: 'ApplyMobile',
      component: ApplyMobile,
      meta: {auth: true}
    },
    {
      path: '/personal/apply/summary',
      name: 'ApplySummary',
      component: ApplySummary,
      meta: {auth: true}
    },
    {
      path: '/personal/apply/title',
      name: 'ApplyTitle',
      component: ApplyTitle,
      meta: {auth: true}
    },
    {
      path: '/personal/apply/domain',
      name: 'ApplyDomain',
      component: ApplyDomain,
      meta: {auth: true}
    },
    /* 我的钱包 */
    {
      path: '/personal/wallet',
      name: 'Wallet',
      component: Wallet,
      meta: {auth: true}
    },
    /* 提现申请 */
    {
      path: '/personal/wallet/detail',
      name: 'WalletDetail',
      component: WalletDetail,
      meta: {auth: true}
    },
    /* 专家认证 */
    {
      path: '/personal/expert',
      name: 'Expert',
      component: Expert,
      meta: {auth: true}
    },
    /* 修改密码-手机验证 */
    {
      path: '/personal/verify',
      name: 'Verify',
      component: Verify,
      meta: {auth: true}
    },
    /* 修改密码 */
    {
      path: '/personal/editpassword',
      name: 'EditPassword',
      component: EditPassword,
      meta: {auth: true}
    },
    /* 消息提醒 */
    {
      path: '/personal/message',
      name: 'Message',
      component: Message,
      meta: {auth: true}
    },
    /* 消息提醒详情 */
    {
      path: '/personal/message/detail',
      name: 'MessageDetail',
      component: MessageDetail,
      meta: {auth: true}
    },
    /* 客户服务 */
    {
      path: '/personal/service',
      name: 'Service',
      component: Service,
      meta: {auth: true}
    },
    /* 关于我们 */
    {
      path: '/personal/about',
      name: 'About',
      component: About,
      meta: {auth: true}
    }
  ]
})

router.beforeEach((to, from, next) => {
  window.scrollTo(0, 0)

  if (to.matched.some(record => record.meta.auth)) {
    if (!Auth.isLogin()) {
      localStorage.removeItem('jwt-token')
      next('/')
    }
  }

  if (to.matched.some(record => record.meta.guest)) {
    if (Auth.isLogin()) {
      next('/main')
    }
  }
  next()
})

export default router



