
import IndexItem from './indexItem/Item.vue'
import API from '@/api'
import XHeader from 'vux/src/components/x-header/index.vue'

import simulationScrollY from '@/components/common/scroll/simulation-scroll-y'

export default {
  data () {
    return {
      treasures: [],
      page: {
        page: 1,
        page_size: 10
      },
      loadMore: {},
      isMore: false
    }
  },
  components: {
    IndexItem,
    XHeader,
    simulationScrollY
  },
  created () {
    this.getMyTreasure()
    this.loadMoreFuc()
  },
  methods: {
    getMyTreasure (page) {
      if (this.page.page !== 1) {
        this.isMore = true
      } else {
        this.$vux.loading.show({
          text: '加载中...'
        })
      }
      API.getMyTreasure(page)
      .then(res => {
        this._pageData(res.data)
        this.$nextTick(() => this.$refs.ssy.loadEnd())
        this.isMore = false
      })
    },
    // 初始化数据
    _pageData (data) {
      this.treasures.push(...data.data)
      this.page.page = data.meta.pagination.current_page + 1
    },
    loadMoreFuc () {
      var that = this
      this.loadMore = {
        distance: 0,
        onLoadMore () {
          that.getMyTreasure(that.page)
        }
      }
    }
  }
}



