
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

import API from '@/api'
import V from '@/api/verify.js'

export default {
  data () {
    return {
      expert: {
        mobile: '',
        email: '',
        idnumber: ''
      },
      cate: [],
      user: {}
    }
  },
  created () {
    this.expert = this.$store.state.expert.expert
    this.getCate()
    this.getSelf()
    console.log(this.expert)
  },
  components: {
    XHeader,
    Cell,
    Group
  },
  methods: {
    // 获取专家分类信息，用于显示
    getCate () {
      API.getCate('expert')
      .then(res => {
        this.cate = res.data.data
      })
    },
    // 获取当前用户信息
    getSelf () {
      API.getSelf()
      .then(res => {
        this.user = res.data.data
        this.expert.realname = this.user.realname
        this.expert.province = this.user.province
        this.expert.city = this.user.city
        this.expert.district = this.user.district
        this.expert.address = this.user.address
        // this.expert.email = this.user.email
        this.expert.mobile = this.user.mobile
      })
    },
    // 申请操作
    applyCtrl () {
      let verify = V.expert(this.expert)
      if (verify.status) {
        API.expertCertifications(this.expert)
        .then(res => {
          this.$vux.toast.text('申请已经提交')
          this.$router.go(-1)
        })
      } else {
        this.$vux.toast.text(verify.text)
      }
    }
  }
}



