
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import bottomAppraisal from './bottomAppraisal/Appraisal'
import API from '@/api'
import wx from 'weixin-js-sdk'
import Swiper from './swiperdetail/Swiper'

export default {
  data () {
    return {
      treasure: {
        user: {
          data: {
            avatar: '',
            realname: ''
          }
        },
        images: {
          data: []
        }
      },
      user: {},
      id: this.$route.query.id,
      showShare: false,
      url: window.location.href,
      imagesList: []
    }
  },
  created () {
    this.getTreasure(this.id)
    this.getSelf()
    this.wechatConfig()
  },
  mounted () {
    this.shareAppCtrl()
    this.shareTimelineCtrl()
  },
  components: {
    XHeader,
    Popup,
    bottomAppraisal,
    Confirm,
    Swiper
  },
  methods: {
    getTreasure (id) {
      this.$vux.loading.show({
        text: '加载中'
      })
      API.getTreasure(id)
      .then(res => {
        this.treasure = res.data.data
        this.imagesList = this.filterImageSrc(this.treasure.images.data)
      })
    },
    getSelf () {
      API.getSelf()
      .then(res => {
        this.user = res.data.data
      })
    },
    // 分享操作
    shareCtrl () {
      this.showShare = !this.showShare
    },
    // 分享给朋友
    shareAppCtrl () {
      let that = this
      wx.ready(() => {
        that.onMenuShareAppMessage()
      })
    },
    // 分享到朋友圈
    shareTimelineCtrl () {
      let that = this
      wx.ready(() => {
        that.onMenuShareTimeline()
      })
    },
    cancelShare () {
      this.showShare = false
    },
    // 鉴定记录
    toHistory () {
      this.$router.push({name: 'History', query: {id: this.id}})
    },
    // 获取微信分享配置
    wechatConfig () {
      let data = {
        url: this.url,
        debug: false,
        menus: 'onMenuShareTimeline,onMenuShareAppMessage'
      }
      API.wechatConfig(data)
      .then(res => {
        wx.config({
          appId: res.data.appId,
          timestamp: res.data.timestamp,
          nonceStr: res.data.nonceStr,
          signature: res.data.signature,
          jsApiList: res.data.jsApiList
        })
      })
    },
    onMenuShareTimeline () {
      let that = this
      wx.onMenuShareTimeline({
        title: that.treasure.name,
        link: that.url,
        imgUrl: that.treasure.image,
        success () {
          that.$vux.toast.text('分享成功')
          this.showShare = false
        },
        cancel () {
          that.$vux.toast.text('分享失败')
        }
      })
    },
    onMenuShareAppMessage () {
      let that = this
      wx.onMenuShareAppMessage({
        title: that.treasure.name,
        desc: that.treasure.describe,
        link: that.url,
        imgUrl: that.treasure.image,
        type: '',
        dataUrl: '',
        success () {
          that.$vux.toast.text('分享成功')
          this.showShare = false
        },
        cancel () {
          that.$vux.toast.text('分享失败')
        }
      })
    },
    filterImageSrc (data) {
      if (data.length !== 0) {
        return data.map(v => {
          v.src = v.url
          v.w = this.getImageWidth(v).width
          v.h = this.getImageWidth(v).height
          return v
        })
      }
    },
    getImageWidth (img) {
      let image = new Image()
      image.src = img.src
      let w = image.width
      let h = image.height
      console.log(w)
      return {
        width: w,
        height: h
      }
    }
  }
}



