
import XHeader from 'vux/src/components/x-header/index.vue'

import CommentItem from './commentItem/Item'
import API from '@/api'
import simulationScrollY from '@/components/common/scroll/simulation-scroll-y'

export default {
  data () {
    return {
      comments: [],
      id: this.$route.query.id,
      content: '',
      page: {
        page: 1,
        page_size: 10
      },
      loadMore: {},
      isMore: false
    }
  },
  components: {
    XHeader,
    CommentItem,
    simulationScrollY
  },
  created () {
    this.getComments()
    this.loadMoreFuc()
  },
  methods: {
    getComments (page) {
      if (this.page.page !== 1) {
        this.isMore = true
      } else {
        this.$vux.loading.show({
          text: '加载中...'
        })
      }
      API.getComments(this.id, page)
      .then(res => {
        if (res.status === 200) {
          this._pageData(res.data)
          this.$nextTick(() => this.$refs.ssy.loadEnd())
          this.isMore = false
        }
      })
    },
    // 初始化数据
    _pageData (data) {
      this.comments.push(...data.data)
      this.page.page = data.meta.pagination.current_page + 1
    },
    loadMoreFuc () {
      var that = this
      this.loadMore = {
        distance: 0,
        onLoadMore () {
          that.getComments(that.page)
        }
      }
    }
  }
}



