const scroll = {
  bind (el, binding) {
    window.addEventListener('scroll', () => {
      if (document.body.scrollTop + window.innerHeight >= el.clientHeight) {
        // el.clientHeight += 20
        let fnc = binding.value
        fnc()
      }
    })
  }
}

export {
  scroll
}



