
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import IndexItem from '../indexItem/Item.vue'

const list = () => ['今日推荐', '最新发布', '附近的']

export default {
  props: {
    treasures: Array,
    isLoading: Boolean
  },
  data () {
    return {
      index: 0,
      content: list()
    }
  },
  components: {
    Tab,
    TabItem,
    IndexItem
  },
  watch: {
    'index' (to, from) {
      this.changeTab(to)
    }
  },
  created () {
    switch (this.$store.state.tab.tab) {
      case 'today':
        this.index = 0
        break
      case 'new':
        this.index = 1
        break
      case 'nearby':
        this.index = 2
        break
    }
  },
  methods: {
    // tab切换
    changeTab (index) {
      switch (index) {
        case 0:
          this.$store.commit('tabIncrement', 'today')
          break
        case 1:
          this.$store.commit('tabIncrement', 'new')
          break
        case 2:
          this.$store.commit('tabIncrement', 'nearby')
          break
      }
    }
  }
}



