
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'

export default {
  data () {
    return {
      content: '',
      id: this.$route.query.id
    }
  },
  components: {
    XHeader
  },
  created () {
  },
  methods: {
    saveCtrl () {
      let obj = {
        content: this.content,
        include: 'source'
      }
      if (this.content !== '') {
        this.$vux.loading.show({
          text: '操作中'
        })
        API.addComment(this.id, obj)
        .then(res => {
          if (res.status === 201) {
            this.$router.go(-1)
          }
        })
      } else {
        this.$vux.toast.text('评论内容不能为空')
      }
    }
  }
}



