
import LoadMore from 'vux/src/components/load-more/index.vue'

// import API from '@/api'

export default {
  props: {
    items: {
      type: Array
    },
    index: {
      type: Number
    }
  },
  data () {
    return {
    }
  },
  components: {
    LoadMore
  },
  computed: {
  },
  methods: {
    comment () {
    },
    praise () {
    },
    toDetail (data, index) {
      var id = null
      switch (index) {
        case 0:
          id = data.treasure_id
          break
        case 1:
          id = data.id
          break
        case 2:
          id = data.id
          break
      }
      this.$router.push({name: 'Detail', query: {id: id}})
    },
    toExpert (data) {
      if (data.expert.data.length !== 0) {
        this.$router.push({name: 'ExpertDetail', query: {id: data.id}})
      }
    }
  }
}



