
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'

export default {
  data () {
    return {
      content: '',
      id: this.$route.query.id
    }
  },
  components: {
    XHeader
  },
  created () {
  },
  methods: {
    saveCtrl () {
      if (this.content !== '') {
        this.$vux.loading.show({
          text: '操作中'
        })
        let obj = {
          content: '@' + this.$route.query.name + ' ' + this.content
        }
        API.addReplay(this.id, obj)
        .then(res => {
          if (res.status === 201) {
            this.$vux.toast.text('回复成功')
            this.$router.go(-1)
          }
        })
      } else {
        this.$vux.toast.text('回复内容不能为空')
      }
    }
  }
}



