
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
  data () {
    return {
      person: this.$store.state.publish.get_from || ''
    }
  },
  components: {
    XHeader
  },
  methods: {
    savePerson () {
      this.$store.commit('getFromIncrement', this.person)
      this.$router.go(-1)
    }
  }
}



