
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'
import V from '@/api/verify.js'

export default {
  data () {
    return {
      user: {
        mobile: '',
        password: ''
      },
      isLoading: false
    }
  },
  components: {
    XHeader
  },
  methods: {
    login () {
      let loginVerify = V.login(this.user)
      this.isLoading = true

      if (loginVerify.status) {
        this.$vux.loading.show({
          text: '操作中...'
        })
        API.login(this.user)
        .then((res) => {
          this.isLoading = false
          if (res.status === 201) {
            this.$vux.toast.text('登录成功')
            localStorage.setItem('jwt-token', res.data.data.token)
            this.$router.push({name: 'Index'})
            window.location.reload()
          }
        })
        .catch(error => {
          this.isLoading = false
          if (error.response.status === 401) {
            this.$vux.toast.text('账号或密码错误')
          }
        })
      } else {
        this.isLoading = false
        this.$vux.toast.text(loginVerify.text)
      }
    }
  }
}



