const state = {
  userinfo: {},
  username: '',
  sex: '',
  age: '',
  address: [],
  mobile: '',
  email: ''
}

const mutations = {
  userinfoIncrement (state, v) {
    state.userinfo = v
  },
  usernameIncrement (state, v) {
    state.username = v
  },
  sexIncrement (state, v) {
    state.sex = v
  },
  ageIncrement (state, v) {
    state.age = v
  },
  addressIncrement (state, v) {
    state.address = v
  },
  mobileIncrement (state, v) {
    state.mobile = v
  },
  emailIncrement (state, v) {
    state.email = v
  }
}

export default {
  state,
  mutations
}



