
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'


export default {
  props: {
    expert: {
      type: Object,
      default: {
        expert: {
          data: {
            price: '',
            summary: ''
          }
        }
      }
    },
    expertCategories: Array,
    evaluations: Array
  },
  computed: {
    cate () {
      // return expert.expertCategories.data
    }
  },
  data () {
    return {
      index: 0,
      content: ['基本信息', '鉴定记录']
    }
  },
  methods: {
    getMyEvaluations () {
    },
    toDetail (treasure) {
      this.$router.push({name: 'HistoryDetail', query: {id: treasure.id, index: treasure.status - 1, expert: true}})
    },
    increment (index) {
      this.$emit('increment', index)
    }
  },
  components: {
    Tab,
    TabItem
  }
}



