
import HeaderNav from '@/components/common/HeaderNav'
import MainNav from '@/components/common/Mainnav'
import InnerNav from './nav/Innernav'
import Tab from './tab/Tab'
import Swiper from './swiper/Swiper'
import Drawer from 'vux/src/components/drawer/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import API from '@/api'
import { scroll } from '@/directives'
import simulationScrollY from '@/components/common/scroll/simulation-scroll-y'

export default {
  data () {
    return {
      selectTag: 'home',
      treasures: [],
      pageTreasures: [],
      busy: false,
      page: {
        page: 1,
        page_size: 5
      },
      loadMore: {},
      isMore: false,
      noMore: false
    }
  },
  created () {
    let tab = this.$store.state.tab.tab
    if (tab === 'today') {
      this.getTreasuresPage('today')
    } else {
      this.$store.commit('tabIncrement', 'today')
    }
    this.loadMoreFuc()
  },
  directives: {
    scroll
  },
  watch: {
    '$store.state.tab.tab' (to) {
      this.page.page = 1
      this.treasures = []
      this.getTreasuresPage(to)
    }
  },
  methods: {
    // 获取宝贝分页
    getTreasuresPage (type) {
      let data = {
        type: type
      }
      if (this.page.page !== 1) {
        this.isMore = true
      } else {
        this.$vux.loading.show({
          text: '加载中...'
        })
      }
      API.getTreasures(data, this.page)
      .then((res) => {
        if (res.data.data.length !== 0) {
          this._pageData(res.data)
          this.$nextTick(() => this.$refs.indexssy.loadEnd())
          this.isMore = false
        } else {
          this.isMore = false
          this.noMore = true
        }
      })
    },
    // 初始化数据
    _pageData (data) {
      this.treasures.push(...data.data)
      this.page.page = data.meta.pagination.current_page + 1
    },
    loadMoreFuc () {
      var that = this
      this.loadMore = {
        distance: 0,
        onLoadMore () {
          that.getTreasuresPage(that.$store.state.tab.tab)
        }
      }
    }
  },
  components: {
    HeaderNav,
    MainNav,
    InnerNav,
    Tab,
    Swiper,
    Drawer,
    Scroller,
    LoadMore,
    simulationScrollY
  }
}



