
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import API from '@/api'
import simulationScrollY from '@/components/common/scroll/simulation-scroll-y'

export default {
  data () {
    return {
      index: Number(this.$route.query.index) || 0,
      content: ['待支付', '待确认', '鉴定中', '已完成'],
      evaluations: [],
      items: [],
      user: {},
      page: {
        page: 1,
        page_size: 10
      },
      loadMore: {},
      isMore: false
    }
  },
  components: {
    XHeader,
    Tab,
    TabItem,
    simulationScrollY
  },
  created () {
    this.getMyEvaluations(this.index)
    this.getSelf()
    this.loadMoreFuc()
  },
  computed: {
    isExpert () {
      return this.user.expert.data.length !== 0
    }
  },
  methods: {
    // 获取我的鉴定数据
    getMyEvaluations (index, page) {
      if (this.page.page !== 1) {
        this.isMore = true
      } else {
        this.$vux.loading.show({
          text: '加载中...'
        })
      }
      let type = this.getType(index)
      API.getMyEvaluations(type, page)
      .then(res => {
        // this.items = res.data.data
        // this.changeTab(index)
        this._pageData(res.data)
        this.$nextTick(() => this.$refs.ssy.loadEnd())
        this.isMore = false
      })
    },
    // 切换tab
    changeTab (index) {
      this.page.page = 1
      this.items = []
      this.getMyEvaluations(index)
      // this.items = this.evaluations.filter(v => {
      //   if (v.status === index + 1) {
      //     return v
      //   }
      // })
    },
    // 查看详情
    toDetail (data) {
      // if (!this.isExpert) {
      //   this.$router.push({name: 'AppraisalDetail', query: {id: data.id, index: this.index}})
      // } else {
      //   this.$router.push({name: 'Detail', query: {id: data.treasure.data.id}})
      // }
      this.$router.push({name: 'AppraisalDetail', query: {id: data.id, index: this.index}})
    },
    // 立即支付
    payCtrl (data) {
      let order = {
        name: data.expert.data.realname,
        id: data.treasure.data.id,
        expert_user_id: data.expert.data.id,
        description: data.expert.data.expert.data.title,
        price: data.expert.data.expert.data.price
      }
      this.$store.commit('orderIncrement', order)
      this.$vux.loading.show({
        text: '加载中...'
      })
      API.getOrder(data.order.data.id)
      .then(res => {
        let order = JSON.stringify(this.$store.state.pay.order)
        this.$router.push({name: 'Pay', query: {orderId: data.order.data.id, order: order}})
      })
    },
    // 删除我的流程
    delEvaluation (id) {
      API.delEvaluation(id)
      .then(res => {
        this.$vux.toast.text('删除鉴定记录成功')
        this.getMyEvaluations()
      })
    },
    // 撤回流程
    userRevoke (id) {
      API.userRevoke(id)
      .then(res => {
        this.$vux.toast.text('撤回鉴定成功')
        this.getMyEvaluations()
      })
    },
    getSelf () {
      API.getSelf()
      .then(res => {
        this.user = res.data.data
      })
    },
    outTime (v) {
      let now = new Date()
      let oldTime = new Date(Date.parse(v.replace(/-/g, '/')))
      let newTime = Math.ceil((now - oldTime) / 1000)
      return 60 * 15 - Number(newTime) > 0
    },
    toExpert (data) {
      if (data.expert.data.length !== 0) {
        this.$router.push({name: 'ExpertDetail', query: {id: data.expert_user_id}})
      }
    },
    getType (index) {
      var type = 'new'
      switch (index) {
        case 0:
          type = 'new'
          break
        case 1:
          type = 'paid'
          break
        case 2:
          type = 'accept'
          break
        case 3:
          type = 'done'
          break
      }
      return type
    },
    _pageData (data) {
      this.items.push(...data.data)
      this.page.page = data.meta.pagination.current_page + 1
    },
    loadMoreFuc () {
      var that = this
      this.loadMore = {
        distance: 0,
        onLoadMore () {
          that.getMyEvaluations(this.index, that.page)
        }
      }
    }
  }
}



