import LoaderImage from './LoaderImage'

export default {
  data () {
    return {
      isLoader: true
    }
  },
  components: {
    LoaderImage
  },
  created () {
    this.loader()
  },
  methods: {
    loader () {
      this.$nextTick(function () {
        setTimeout(() => {
          this.isLoader = false
        }, 1000)
      })
    }
  }
}



