
import XHeader from 'vux/src/components/x-header/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'


export default {
  data () {
    return {
      privacy: [
        {
          key: 'all',
          value: '所有人可见'
        },
        {
          key: 'expert',
          value: '鉴宝专家可见'
        },
        {
          key: 'self',
          value: '仅自己可见'
        }
      ],
      selectTag: this.$store.state.publish.visible || ''
    }
  },
  components: {
    XHeader,
    Group,
    Radio
  },
  watch: {
    'selectTag' (to) {
      this.$store.commit('visibleIncrement', this.selectTag)
      this.$router.go(-1)
    }
  },
  methods: {
    change () {
      // this.$store.commit('increment', this.selectTag)
      // this.$router.go(-1)
    }
  }
}



