import MainNav from '@/components/common/Mainnav'
// import API from '@/api'

export default {
  data () {
    return {
      transitionName: '',
      url: window.location.href
    }
  },
  watch: {
    '$route' (to, from) {
      const toDepth = to.path.split('/').length
      const fromDepth = from.path.split('/').length
      if (toDepth === fromDepth) {
        this.transitionName = 'page'
      } else if (toDepth < fromDepth) {
        this.transitionName = 'page-out'
      } else {
        this.transitionName = 'page-in'
      }
    }
  },
  created () {
    let openid = this._parseQuery(window.location.href).openid
    if (typeof openid !== 'undefined') {
      console.log('执行存储')
      localStorage.setItem('openid', openid)
    }
    // this.wxOauth()
  },
  methods: {
    _parseQuery (query) {
      // 解析url
      let args = {}
      let newQuery = query.replace('?', '&')
      let items = newQuery.split('&')

      let item = null
      let name = null
      let value = null
      for (let i = 0; i < items.length; i++) {
        item = items[i].split('=')
        if (item[0]) {
          name = item[0]
          value = item[1] ? item[1] : ''
          args[name] = value
        }
      }
      return args
    }
    // 微信授权
    // wxOauth () {
    //   API.wxOauth(this.url)
    //   .then(res => {
    //     console.log(res.data)
    //     localStorage.setItem('openid', res.data.data.openid)
    //   })
    // }
  },
  components: {
    MainNav
  }
}



