
import API from '@/api'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
  props: {
    praise: {
      type: Number
    },
    comment: {
      type: Number
    },
    id: {
      type: Number
    },
    owner: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      isParise: false,
      showConfirm: false
    }
  },
  mounted () {
    this.getFavoritesStatus()
  },
  directives: {
    TransferDom
  },
  methods: {
    commentCtrl () {
      this.$router.push({name: 'Comment', query: {id: this.id}})
    },
    // 点赞状态
    getFavoritesStatus () {
      API.getFavoritesStatus(this.$route.query.id)
      .then(res => {
        if (res.data.status === 'exist') {
          this.isParise = true
        } else {
          this.isParise = false
        }
      })
    },
    // 点赞
    addFavorites () {
      API.addFavorites(this.id)
      .then(res => {
        console.log('点赞成功')
        this.isParise = true
        this.praise += 1
      })
    },
    // 取消点赞
    delFavorites () {
      API.delFavorites(this.id)
      .then(res => {
        console.log('取消点赞成功')
        this.isParise = false
        this.praise -= 1
      })
    },
    // 点赞列表
    getFavorites () {

    },
    Appraisal () {
      if (this.owner) {
        this.$router.push({name: 'ExpertList', query: {treasureId: this.$route.query.id}})
      } else {
        this.showConfirm = true
      }
    },
    onConfirm () {
      this.$router.push({name: 'Upload'})
    }
  },
  components: {
    Confirm
  }
}



