
import XHeader from 'vux/src/components/x-header/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import API from '@/api'
import simulationScrollY from '@/components/common/scroll/simulation-scroll-y'

export default {
  data () {
    return {
      isEdit: true,
      messages: [],
      checkedArr: [],
      page: {
        page: 1,
        page_size: 10
      },
      loadMore: {},
      isMore: false
    }
  },
  components: {
    XHeader,
    Badge,
    simulationScrollY
  },
  created () {
    this.getNotifications()
    this.loadMoreFuc()
  },
  methods: {
    editCtrl () {
      this.isEdit = !this.isEdit
    },
    deleteCtrl () {
      API.delNotifications(this.checkedArr.join())
      .then(res => {
        this.$vux.toast.text('删除消息成功')
        this.messages = []
        this.getNotifications()
      })
    },
    // 消息列表
    getNotifications (page) {
      if (this.page.page !== 1) {
        this.isMore = true
      } else {
        this.$vux.loading.show({
          text: '加载中...'
        })
      }
      API.getNotifications(page)
      .then(res => {
        this._pageData(res.data)
        this.$nextTick(() => this.$refs.ssy.loadEnd())
        this.isMore = false
      })
    },
    toDetail (data) {
      if (this.isEdit) {
        API.readNotification(data.id)
        .then(() => {
          this.$router.push({name: 'MessageDetail', query: {id: data.id}})
        })
      }
    },
    // 初始化数据
    _pageData (data) {
      this.messages.push(...data.data)
      this.page.page = data.meta.pagination.current_page + 1
    },
    loadMoreFuc () {
      var that = this
      this.loadMore = {
        distance: 0,
        onLoadMore () {
          that.getNotifications(that.page)
        }
      }
    }
  }
}



