
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'
import V from '@/api/verify.js'

export default {
  data () {
    return {
      user: {
        mobile: '',
        password: '',
        code: ''
      },
      isLoading: false,
      isSending: true,
      sendText: '进行验证'
    }
  },
  created () {
    this.geetestConfig()
  },
  methods: {
    signup () {
      let signupVerify = V.signup(this.user)
      this.isLoading = true
      if (signupVerify.status) {
        this.$vux.loading.show({
          text: '操作中...'
        })
        API.signup(this.user)
        .then((res) => {
          this.isLoading = false
          if (res.status === 201) {
            this.$vux.toast.text('注册成功')
            this.$router.push({name: 'Signin'})
          }
        })
      } else {
        this.isLoading = false
        this.$vux.toast.text(signupVerify.text)
      }
    },
    sendCode () {
      let phoneVerify = V.phone(this.user)
      if (phoneVerify.status) {
        API.sendCode(this.user)
        .then(res => {
          if (res.status === 204) {
            this.codeBtnCtrl()
          }
        })
      } else {
        this.$vux.toast.text(phoneVerify.text)
      }
    },
    codeBtnCtrl () {
      var time = 60
      this.isSending = true
      let timer = setInterval(() => {
        if (time > 0) {
          this.sendText = time + '重新获取'
          time -= 1
        } else {
          this.isSending = false
          this.sendText = '重新获取'
          clearInterval(timer)
        }
      }, 1000)
    },
    // geetest验证参数
    geetestConfig () {
      let data = {
        user_id: this.user.mobile,
        client_type: 'web'
      }
      API.geetestConfig(data)
      .then(res => {
        window.initGeetest({
          gt: res.data.gt,
          challenge: res.data.challenge,
          offline: true,
          new_captcha: res.data.new_captcha,
          width: '100%'
        }, captchaObj => {
          captchaObj.appendTo('#captcha-box')
          captchaObj.onSuccess(() => {
            this.isSending = false
            this.sendText = '发送验证码'
          })
        })
      })
    }
  },
  components: {
    XHeader
  }
}



