
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import value2name from 'vux/src/filters/value2name.js'
import Datetime from 'vux/src/components/datetime/index.vue'

import API from '@/api'

export default {
  data () {
    return {
      addressData: ChinaAddressV3Data,
      user: {
        name: '',
        sex: '',
        age: '',
        address: [],
        mobile: '',
        email: '',
        expert: {
          data: []
        }
      },
      address: [],
      cate: ''
    }
  },
  components: {
    XHeader,
    Cell,
    Group,
    XAddress,
    ChinaAddressV3Data,
    Datetime
  },
  created () {
    this.getSelf()
    this.getCate()
  },
  computed: {
    // 是否是专家
    isExpert () {
      if (this.user.expert.data.length !== 0) {
        return true
      } else {
        return false
      }
    },
    filterDate () {
      return this.user.created_at.substring(0, 10)
    }
  },
  watch: {
    'address' (to) {
      const ads = this.getName(to).split(' ')
      this.user.province = ads[0]
      this.user.city = ads[1]
      this.user.district = ads[2]
      // this.updateUser()
    }
  },
  methods: {
    // 转换地址名称
    getName (value) {
      if (value.length !== 0) {
        return value2name(value, ChinaAddressV3Data)
      }
    },
    // 获取当前用户信息
    getSelf () {
      this.$vux.loading.show({
        text: '加载中...'
      })
      API.getSelf()
      .then(res => {
        this.user = res.data.data
        this.address = [this.user.province, this.user.city, this.user.district]
        this.$store.commit('userinfoIncrement', this.user)
      })
    },
    // 上传头像图片
    uploadCtrl (event) {
      let files = event.target.files
      let form = new FormData()
      form.append('avatar', files[0])
      API.uploadAvatar(form)
      .then(res => {
        if (res.status === 201) {
          this.user.avatar = res.data.url
        }
      })
    },
    // 更新用户数据
    updateUser () {
      API.updateUser(this.user)
      .then(res => {
        this.getSelf()
      })
    },
    // 改变日期
    changeDate () {
      // this.updateUser()
    },
    // 保存操作
    saveCtrl () {
      API.updateUser(this.user)
      .then(res => {
        if (res.status === 200) {
          this.$vux.toast.text('修改成功')
        }
      })
    },
    // 获取专家分类
    getCate () {
      API.getCate('expert')
      .then(res => {
        let cateArr = res.data.data
        if (cateArr.length > 0) {
          this.cate = cateArr.map(v => v.name).join()
        }
      })
    }
  }
}



