
import ExpertListItem from './expertListItem/Item'
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'
import simulationScrollY from '@/components/common/scroll/simulation-scroll-y'

export default {
  data () {
    return {
      selectTag: 'expert',
      isShow: false,
      experts: [],
      page: {
        page: 1,
        page_size: 10
      },
      loadMore: {},
      isMore: false,
      userId: null
    }
  },
  components: {
    ExpertListItem,
    XHeader,
    simulationScrollY
  },
  created () {
    this.getExperts()
    this.loadMoreFuc()
    this.getSelf()
  },
  methods: {
    getExperts (page) {
      if (this.page.page !== 1) {
        this.isMore = true
      } else {
        this.$vux.loading.show({
          text: '加载中...'
        })
      }
      API.getExperts(page)
      .then(res => {
        this._pageData(res.data)
        this.$nextTick(() => this.$refs.ssy.loadEnd())
        this.isMore = false
      })
    },
    // 初始化数据
    _pageData (data) {
      this.experts.push(...data.data)
      this.page.page = data.meta.pagination.current_page + 1
    },
    loadMoreFuc () {
      var that = this
      this.loadMore = {
        distance: 0,
        onLoadMore () {
          that.getExperts(that.page)
        }
      }
    },
    // 获取自己的信息
    getSelf () {
      API.getSelf()
      .then(res => {
        this.userId = res.data.data.id
      })
    }
  }
}



