
import Vue from 'vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import value2name from 'vux/src/filters/value2name.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import API from '@/api'
import V from '@/api/verify.js'

const treasure = {
  name: '',
  describe: '',
  visible: '',
  cate: {
    key: null,
    value: ''
  },
  get_at: '',
  get_where: [],
  get_from: '',
  get_price: '',
  images: []
}

export default {
  data () {
    return {
      selectTag: 'upload',
      addressData: ChinaAddressV3Data,
      treasure: treasure,
      showConfirm: false,
      treasureId: '',
      isEditImage: true
    }
  },
  directives: {
    TransferDom
  },
  components: {
    XHeader,
    Confirm,
    Cell,
    Group,
    Datetime,
    XAddress
  },
  created () {
    this.getStoreValue()
    this.getSelf()
  },
  watch: {
    'treasure.name' (to) {
      this.$store.commit('nameIncrement', to)
    },
    'treasure.describe' (to) {
      this.$store.commit('describeIncrement', to)
    },
    'treasure.get_at' (to) {
      this.$store.commit('getAtIncrement', to)
    },
    'treasure.get_where' (to) {
      this.$store.commit('getWhereIncrement', to)
    },
    'treasure.images' (to) {
      this.$store.commit('imagesIncrement', to)
    }
  },
  methods: {
    // 转换地址名称
    getName (value) {
      if (value.length !== 0) {
        return value2name(value, ChinaAddressV3Data)
      }
    },
    // 取消操作
    cancelCtrl () {
      this.$router.go(-1)
    },
    // 发布
    publishCtrl () {
      let data = this.treasure
      let address = data.get_where
      data.category_id = this.treasure.cate.key
      data.get_where = this.getName(data.get_where)

      let uploadVerify = V.upload(this.treasure)
      if (uploadVerify.status) {
        API.addTreasure(data)
        .then(res => {
          if (res.status === 201) {
            this.showConfirm = true
            this.treasure.get_where = address
            this.clearData()
            this.treasureId = res.data.data.id
          }
        })
      } else {
        this.$vux.toast.text(uploadVerify.text)
      }
    },
    // 上传图片
    uploadCtrl (event) {
      let files = event.target.files
      let form = new FormData()
      this.$vux.loading.show({
        text: '上传图片中...'
      })
      form.append('image', files[0])
      API.uploadImage(form)
      .then(res => {
        if (res.status === 201) {
          this.treasure.images.push(res.data.url)
        }
      })
      // for (let file of files) {
      //   let data = new FormData()
      //   data.append('image', file)
            //   API.uploadImage(data)
      //   .then(res => {
      //     this.$vux.loading.hide()
      //     if (res.status === 201) {
      //       this.treasure.images.push(res.data.url)
      //     }
      //   })
      // }
    },
    // 获取store的值
    getStoreValue () {
      let store = this.$store.state.publish
      this.treasure = store
    },
    // 弹窗操作
    onCancel () {
      this.$router.push({name: 'ExpertList', query: {treasureId: this.treasureId}})
    },
    onConfirm () {
      this.$router.push({name: 'Index'})
    },
    // 清空数据
    clearData () {
      this.$store.commit('nameIncrement', '')
      this.$store.commit('describeIncrement', '')
      this.$store.commit('visibleIncrement', '')
      this.$store.commit('cateIncrement', '')
      this.$store.commit('getAtIncrement', '')
      this.$store.commit('getWhereIncrement', [])
      this.$store.commit('getFromIncrement', '')
      this.$store.commit('getPriceIncrement', '')
      this.$store.commit('imagesIncrement', [])
    },
    delCtrl (i) {
      this.treasure.images.splice(i, 1)
    },
    moveLeftCtrl (i) {
      this.treasure.images.forEach((value, index) => {
        if (i === index) {
          this.treasure.images.splice(index, 1)
          this.treasure.images.splice(index - 1, 0, value)
        }
      })
    },
    moveRightCtrl (i) {
      this.treasure.images.forEach((value, index) => {
        if (i === index) {
          this.treasure.images.splice(index, 1)
          this.treasure.images.splice(index + 1, 0, value)
        }
      })
    },
    editImg () {
      this.isEditImage = !this.isEditImage
    },
    getSelf () {
      API.getSelf()
      .catch(error => {
        if (error.response.status === 401) {
          localStorage.removeItem('jwt-token')
          this.$router.push({name: 'Loader'})
        }
      })
    }
  }
}

// 隐私设置过滤
Vue.filter('privacySetting', (value) => {
  let privacy = ''
  switch (value) {
    case 'all':
      privacy = '所有人可见'
      break
    case 'expert':
      privacy = '鉴宝专家可见'
      break
    case 'self':
      privacy = '仅自己可见'
      break
  }
  return privacy
})



