
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'

export default {
  data () {
    return {
      menu: [],
      activeMenu: [],
      selectTag: this.$store.state.publish.cate.key || ''
    }
  },
  created () {
    let menuStore = this.$store.state.cate.menu
    let index = this.$store.state.cate.menuIndex
    if (menuStore.length !== 0) {
      this.menu = menuStore
      this.menu[index].checked = true
      this.activeMenu = this.menu[index]
    } else {
      this._getCate()
    }
  },
  methods: {
    // 选择父分类
    selectCate (m, i) {
      this.menu.forEach((v) => {
        v.checked = false
      })
      m.checked = true
      this.activeMenu = m
      this.$store.commit('menuIndexIncrement', i)
    },
    // 选择子分类
    selectChildCate (m, i) {
      // 选中的子分类
      this.clearChildCateChecked()
      m.checked = true
      let obj = {
        key: m.id,
        value: m.name
      }
      this.$store.commit('cateIncrement', obj)
      this.$router.go(-1)
    },
    // 获取分类数据
    _getCate () {
      this.$vux.loading.show({
        text: '加载中...'
      })
      API.getCate('treasure')
      .then((res) => {
        if (res.status === 200) {
          this.menu = this.filterCate(res.data.data)
          this.menu[0].checked = true
          this.activeMenu = this.menu[0]
          this.$vux.loading.hide()
          this.$store.commit('menuIncrement', this.menu)
        }
      })
    },
    // 处理分类数据
    filterCate (cate) {
      if (cate.length !== 0) {
        cate.forEach(v => {
          v.checked = false
          this.filterChildCate(v.child)
        })
        return cate
      }
    },
    filterChildCate (childCate) {
      if (childCate.length !== 0) {
        childCate.forEach(v => {
          v.checked = false
        })
      }
    },
    clearChildCateChecked () {
      this.menu.forEach(v => {
        this.filterChildCate(v.child)
      })
    }
  },
  components: {
    XHeader
  }
}



