
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'
import V from '@/api/verify.js'

export default {
  data () {
    return {
      user: this.$store.state.user.userinfo || {}
    }
  },
  components: {
    XHeader
  },
  created () {
  },
  methods: {
    saveCtrl () {
      let verify = V.username(this.user.realname)
      if (verify.status) {
        API.updateUser(this.user)
        .then(res => {
          if (res.status === 200) {
            this.$vux.toast.text('修改成功')
            this.$router.go(-1)
          }
        })
      } else {
        this.$vux.toast.text(verify.text)
      }
    }
  }
}



