
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

import API from '@/api'

export default {
  data () {
    return {
      expert: {
        expert: {
          data: {
            money: 0.00
          }
        }
      },
      logs: []
    }
  },
  components: {
    XHeader,
    Cell,
    Group
  },
  created () {
    this.getSelf()
    this.getWithdraws()
  },
  methods: {
    // 获取专家信息
    getSelf () {
      API.getSelf()
      .then(res => {
        this.expert = res.data.data
      })
    },
    // 专家的余额变化日志
    moneyLogs () {
      API.moneyLogs()
      .then(res => {
        this.logs = res.data.data
      })
    },
    // 提现记录
    getWithdraws () {
      API.getWithdraws()
      .then(res => {
        this.logs = res.data.data
      })
    }
  }
}



