
import XHeader from 'vux/src/components/x-header/index.vue'

import V from '@/api/verify.js'

export default {
  data () {
    return {
      expert: this.$store.state.expert.expert || {}
    }
  },
  components: {
    XHeader
  },
  created () {
  },
  methods: {
    saveCtrl () {
      let verify = V.price(this.expert.price)
      if (verify.status) {
        this.$store.commit('expertIncrement', this.expert)
        this.$router.go(-1)
      } else {
        this.$vux.toast.text(verify.text)
      }
    }
  }
}



