export default {
  name: 'set-timer',
  props: {
    timer: {
      type: Number
    }
  },
  data () {
    return {
      t: this.timer,
      timestop: 30,
      Interval: null
    }
  },
  mounted () {
    if (this.t < 0) {
      this.$vux.toast.text('支付超时')
      this.$router.go(-1)
    } else {
      this.starttimer()
    }
  },
  computed: {
    computedTime () {
      if (this.t < 60 && this.t > 0) {
        return '<span>' + this.t + '秒</span>'
      } else if (this.t <= 0) {
        return '<span>超时</span>'
      } else {
        let min = Math.floor(this.t / 60)
        let sec = this.t % 60
        return '<span>' + min + ' 分 ' + sec + ' 秒</span>'
      }
    }
  },
  methods: {
    starttimer () {
      this.Interval = setInterval(() => {
        if (this.t <= 0) {
          clearInterval(this.Interval)
        } else {
          this.t--
          // console.log(this.initTime(this.timer))
        }
      }, 1000)
    }
  }
}



