const state = {
  name: '',
  describe: '',
  visible: 'all',
  cate: '',
  get_at: '',
  get_where: [],
  get_from: '',
  get_price: '',
  images: []
}

const mutations = {
  nameIncrement (state, v) {
    state.name = v
  },
  describeIncrement (state, v) {
    state.describe = v
  },
  visibleIncrement (state, v) {
    state.visible = v
  },
  cateIncrement (state, v) {
    state.cate = v
  },
  getAtIncrement (state, v) {
    state.get_at = v
  },
  getWhereIncrement (state, v) {
    state.get_where = v
  },
  getFromIncrement (state, v) {
    state.get_from = v
  },
  getPriceIncrement (state, v) {
    state.get_price = v
  },
  imagesIncrement (state, v) {
    state.images = v
  }
}

export default {
  state,
  mutations
}



