
import XHeader from 'vux/src/components/x-header/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import IndexItem from './indexItem/Item'
import API from '@/api'
import simulationScrollY from '@/components/common/scroll/simulation-scroll-y'

export default {
  data () {
    return {
      treasures: [],
      menu: this.$store.state.cate.activeMenu || [],
      childMenu: this.$store.state.cate.childMenu || {},
      pageSize: 10,
      allActive: false,
      emptyTip: '暂时没有宝贝',
      page: {
        page: 1,
        page_size: 10
      },
      loadMore: {},
      isMore: false,
      cateId: this.$store.state.cate.activeMenu[0].up_id
    }
  },
  components: {
    XHeader,
    IndexItem,
    LoadMore,
    simulationScrollY
  },
  created () {
    this.getCateTreasures(this.childMenu.id)
    this.loadMoreFuc()
  },
  mounted () {
    this.cancelChecked()
    this.defaultSelect()
  },
  methods: {
    // 获取数据
    getCateTreasures (id, page) {
      if (this.page.page !== 1) {
        this.isMore = true
      } else {
        this.$vux.loading.show({
          text: '加载中...'
        })
      }
      API.getCateTreasures(id, page)
      .then(res => {
        if (res.status === 200) {
          this._pageData(res.data)
          this.$nextTick(() => this.$refs.ssy.loadEnd())
          this.isMore = false
        }
      })
    },
    // 选择分类
    selectMenu (m) {
      this.treasures = []
      this.$vux.loading.show({
        text: '加载中...'
      })
      this.cancelChecked()
      m.checked = true
      this.allActive = false
      this.cateId = m.id
      this.getCateTreasures(m.id)
    },
    // 选择全部分类
    selectAllMenu () {
      this.treasures = []
      this.$vux.loading.show({
        text: '加载中...'
      })
      this.cancelChecked()
      this.allActive = true
      this.cateId = this.menu[0].up_id
      this.getCateTreasures(this.menu[0].up_id)
    },
    // 默认选中标示
    cancelChecked () {
      if (this.menu.length !== 0) {
        this.menu.forEach(v => {
          v.checked = false
        })
      }
    },
    // 跳转状态的选中
    defaultSelect () {
      if (this.$route.params.cateAll) {
        this.allActive = true
      } else {
        this.menu[this.$route.params.cateId].checked = true
      }
    },
    _pageData (data) {
      this.treasures.push(...data.data)
      this.page.page = data.meta.pagination.current_page + 1
    },
    loadMoreFuc () {
      var that = this
      this.loadMore = {
        distance: 0,
        onLoadMore () {
          that.getCateTreasures(that.cateId, that.page)
        }
      }
    }
  }
}



