
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

import SetTimer from '@/components/common/settimer/SetTimer'
import API from '@/api'

export default {
  data () {
    return {
      // order: this.$store.state.pay.order,
      order: JSON.parse(this.$route.query.order),
      orderId: this.$route.query.orderId,
      // outTradeNo: this.$route.query.out_trade_no,
      timer: 15 * 60,
      imgSrc: '',
      isPayImage: false,
      intervalTimer: null,
      url: window.location.href
    }
  },
  components: {
    XHeader,
    Cell,
    Group,
    SetTimer
  },
  created () {
    this.getOrderTime()
  },
  mounted () {
    if (this.timer < 0) {
      clearInterval(this.Interval)
      this.$vux.toast.text('支付超时')
      this.$router.go(-1)
    } else {
      this.starttimer()
    }
  },
  computed: {
    // 时间组件
    computedTime () {
      if (this.timer < 60 && this.timer > 0) {
        return '<span>' + this.timer + '秒</span>'
      } else if (this.timer <= 0) {
        return '<span>超时</span>'
      } else {
        let min = Math.floor(this.timer / 60)
        let sec = this.timer % 60
        return '<span>' + min + ' 分 ' + sec + ' 秒</span>'
      }
    }
  },
  methods: {
    pay () {
      let openid = localStorage.getItem('openid')
      if (openid === null) {
        API.wxPay(this.orderId, 'NATIVE')
        .then(res => {
          this.imgSrc = res.data.url
          this.isPayImage = true
          this.polling()
        })
      } else {
        API.wxPay(this.orderId, 'JSAPI', openid)
        .then(res => {
          // alert(res.data)
          // for (let [k, v] of Object.entries(res.data.config)) {
          //   alert(k + ':' + v)
          // }
          if (typeof window.WeixinJSBridge === 'undefined') {
            if (document.addEventListener) {
              document.addEventListener('WeixinJSBridgeReady', this._wxOnBridgeReady(res.data.config), false)
            } else if (document.attachEvent) {
              document.attachEvent('WeixinJSBridgeReady', this._wxOnBridgeReady(res.data.config))
              document.attachEvent('onWeixinJSBridgeReady', this._wxOnBridgeReady(res.data.config))
            }
          } else {
            this._wxOnBridgeReady(res.data.config)
          }
        })
      }
    },
    polling () {
      this.intervalTimer = setInterval(() => {
        this.getOrder()
      }, 1000)
    },
    // 获取订单信息是否支付成功
    getOrder () {
      API.getOrder(this.orderId)
      .then(res => {
        if (res.data.data.status === 1) {
          this.$vux.toast.text('支付成功')
          clearInterval(this.intervalTimer)
          this.$router.go(-1)
        } else if (res.data.data.status === 2) {
          this.$vux.toast.text('支付取消')
          clearInterval(this.intervalTimer)
        }
      })
    },
    _wxOnBridgeReady (paySign) {
      // 微信调用支付
      // for (let [k, v] of Object.entries(paySign)) {
      //   alert(k + ':' + v)
      // }
      window.WeixinJSBridge.invoke('getBrandWCPayRequest', {
        'appId': paySign.appId,
        'timeStamp': paySign.timeStamp,
        'nonceStr': paySign.nonceStr,
        'package': paySign.package,
        'signType': paySign.signType,
        'paySign': paySign.paySign
      }, (res) => {
        // for (let [k, v] of Object.entries(res)) {
        //   alert(k + ':' + v)
        // }
        if (res.err_msg === 'get_brand_wcpay_request:ok') {
          this.$vux.toast.text('支付成功')
          this.$router.go(-1)
        } else {
          this.$vux.toast.text('支付取消')
        }
      })
    },
    // 获取支付时间
    getOrderTime () {
      API.getOrder(this.orderId)
      .then(res => {
        this.timer = this.initTime(res.data.data.created_at)
        this.starttimer()
      })
    },
    // 初始化时间
    initTime (time) {
      let now = new Date()
      let oldTime = new Date(Date.parse(time.replace(/-/g, '/')))
      let newTime = Math.ceil((now - oldTime) / 1000)
      return 60 * 15 - Number(newTime)
    },
    // 开始计时间
    starttimer () {
      this.Interval = setInterval(() => {
        if (this.timer <= 0) {
          clearInterval(this.Interval)
          this.$vux.toast.text('支付超时')
          this.$router.go(-1)
        } else {
          this.timer--
        }
      }, 1000)
    }
  }
}



