
import XHeader from 'vux/src/components/x-header/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'

import API from '@/api'

export default {
  data () {
    return {
      sexes: ['男', '女'],
      user: this.$store.state.user.userinfo || {}
    }
  },
  components: {
    XHeader,
    Radio,
    Group
  },
  created () {
  },
  watch: {
    'user.sex' (to) {
      this.saveCtrl()
    }
  },
  methods: {
    saveCtrl () {
      API.updateUser(this.user)
      .then(res => {
        if (res.status === 200) {
          this.$vux.toast.text('修改成功')
          this.$router.go(-1)
        }
      })
    }
  }
}



