
import XHeader from 'vux/src/components/x-header/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import API from '@/api'

export default {
  data () {
    return {
      treasure: {
        user: {
          data: {}
        }
      },
      user: {},
      id: this.$route.query.id,
      treasureId: this.$route.query.treasureId,
      evaluations: {}
    }
  },
  created () {
    this.getTreasure(this.treasureId)
    this.getSelf()
    this.getEvaluationsHistoryDetail(this.id)
  },
  components: {
    XHeader,
    Confirm
  },
  methods: {
    getTreasure (id) {
      this.$vux.loading.show({
        text: '加载中'
      })
      API.getTreasure(id)
      .then(res => {
        this.treasure = res.data.data
      })
    },
    getSelf () {
      API.getSelf()
      .then(res => {
        this.user = res.data.data
      })
    },
    getEvaluationsHistoryDetail (id) {
      API.getEvaluationsHistoryDetail(id)
      .then(res => {
        this.evaluations = res.data.data
      })
    }
  }
}



