
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import API from '@/api'

export default {
  data () {
    return {
      id: this.$route.query.id,
      index: this.$route.query.index,
      evaluation: {
        treasure: {
          data: {
            image: '',
            created_at: '',
            describe: ''
          }
        },
        user: {
          data: {
            avatar: '',
            realname: ''
          }
        },
        expert: {
          data: {
            avatar: '',
            realname: '',
            expertCategories: {
              data: []
            },
            expert: {
              data: {
                title: ''
              }
            }
          }
        }
      },
      imagesList: []
    }
  },
  components: {
    XHeader,
    Group,
    Cell
  },
  created () {
    if (this.$route.query.expert) {
      this.getExpertEvaluation()
    } else {
      this.getEvaluationsHistoryDetail()
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    // 获取鉴定详情
    getEvaluationsHistoryDetail () {
      this.$vux.loading.show({
        text: '加载中...'
      })
      API.getEvaluationsHistoryDetail(this.id)
      .then(res => {
        this.evaluation = res.data.data
        this.imagesList = this.filterImageSrc(this.evaluation.treasure.data.images.data)
      })
    },
    getExpertEvaluation () {
      API.getExpertEvaluation(this.id)
      .then(res => {
        this.evaluation = res.data.data
      })
    },
    filterImageSrc (data) {
      if (data.length !== 0) {
        return data.map(v => {
          v.src = v.url
          v.w = this.getImageWidth(v).width
          v.h = this.getImageWidth(v).height
          return v
        })
      }
    },
    getImageWidth (img) {
      let image = new Image()
      image.src = img.src
      let w = image.width
      let h = image.height
      return {
        width: w,
        height: h
      }
    }
  }
}



