
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import IndexItem from './indexItem/Item'
import ExpertItem from '@/components/expert/expertItem/Item'
import API from '@/api'
import simulationScrollY from '@/components/common/scroll/simulation-scroll-y'

export default {
  data () {
    return {
      index: 0,
      content: ['宝物', '专家'],
      treasures: [],
      experts: [],
      searchStr: '',
      treasurePage: {
        page: 1,
        page_size: 5
      },
      expertPage: {
        page: 1,
        page_size: 10
      },
      loadMoreTreasure: {},
      loadMoreExpert: {},
      isMore: false
    }
  },
  watch: {
    'searchStr' (to) {
      if (to !== '') {
        if (this.index === 0) {
          this.treasures = []
          this.treasurePage.page = 1
          this.searchTreasure(to)
        } else {
          this.experts = []
          this.expertPage.page = 1
          this.searchExpert(to)
        }
      }
    },
    'index' (to) {
      // this.searchStr = ''
      if (to === 0) {
        this.treasures = []
        this.treasurePage.page = 1
        this.searchTreasure(this.searchStr)
      } else {
        this.experts = []
        this.expertPage.page = 1
        this.searchExpert(this.searchStr)
      }
    }
  },
  components: {
    Tab,
    TabItem,
    IndexItem,
    ExpertItem,
    simulationScrollY
  },
  created () {
    this.loadMoreTreasureFuc()
    this.loadMoreExpertFuc()
  },
  methods: {
    // 搜索宝贝
    searchTreasure (keywords, page) {
      if (this.treasurePage.page !== 1) {
        this.isMore = true
      } else {
        this.$vux.loading.show({
          text: '加载中...'
        })
      }
      API.searchTreasure(keywords, page)
      .then(res => {
        this.treasures.push(...res.data.data)
        this.treasurePage.page = res.data.meta.pagination.current_page + 1
        this.$nextTick(() => this.$refs.treasureSsy.loadEnd())
        this.isMore = false
      })
    },
    // 搜索专家
    searchExpert (keywords, page) {
      if (this.expertPage.page !== 1) {
        this.isMore = true
      } else {
        this.$vux.loading.show({
          text: '加载中...'
        })
      }
      API.searchExpert(keywords, page)
      .then(res => {
        this.experts.push(...res.data.data)
        this.expertPage.page = res.data.meta.pagination.current_page + 1
        this.$nextTick(() => this.$refs.expertSsy.loadEnd())
        this.isMore = false
      })
    },
    // 取消操作
    cancelCtrl () {
      this.$router.go(-1)
    },
    clearCtrl () {
      this.searchStr = ''
    },
    loadMoreTreasureFuc () {
      var that = this
      this.loadMoreTreasure = {
        distance: 0,
        onLoadMore () {
          that.searchTreasure(that.searchStr, that.treasurePage)
        }
      }
    },
    loadMoreExpertFuc () {
      var that = this
      this.loadMoreExpert = {
        distance: 0,
        onLoadMore () {
          that.searchExpert(that.searchStr, that.expertPage)
        }
      }
    }
  }
}



