
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'
import V from '@/api/verify.js'

export default {
  data () {
    return {
      user: {
        old_password: '',
        password: '',
        password_confirmation: ''
      },
      isLoading: false
    }
  },
  created () {
  },
  methods: {
    editPassword () {
      let editPasswordVerify = V.editPassword(this.user)
      this.isLoading = true
      if (editPasswordVerify.status) {
        API.editPassword(this.user)
        .then(res => {
          this.isLoading = false
          this.$vux.toast.text('修改密码成功')
          this.$router.push({name: 'PersonalIndex'})
        })
        .catch(error => {
          this.isLoading = false
          if (error.response.data.code === 102) {
            this.$vux.toast.text('原始密码不正确')
          }
        })
      } else {
        this.isLoading = false
        this.$vux.toast.text(editPasswordVerify.text)
      }
    }
  },
  components: {
    XHeader
  }
}



