
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import API from '@/api'
import V from '@/api/verify.js'

export default {
  data () {
    return {
      index: Number(this.$route.query.index) || 0,
      id: this.$route.query.id,
      evaluation: {
        expert: {
          data: {
            summary: '',
            created_at: '',
            evaluation_done_count: '',
            realname: '',
            avatar: '',
            expert: {
              data: {
                title: ''
              }
            },
            expertCategories: {
              data: []
            }
          }
        },
        treasure: {
          data: {
            image: '',
            created_at: '',
            describe: ''
          }
        },
        user: {
          data: {
            avatar: '',
            realname: ''
          }
        }
      },
      eva: {
        content: '',
        academic_name: '',
        years: '',
        evaluate: '',
        historical_value: '',
        authenticity: ''
      },
      imagesList: []
    }
  },
  components: {
    XHeader,
    Group,
    Cell,
    Datetime
  },
  created () {
    this.getEvaluationsHistoryDetail()
    this.eva = this.$store.state.eva.eva
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    // 获取鉴定详情
    getEvaluationsHistoryDetail () {
      this.$vux.loading.show({
        text: '加载中...'
      })
      API.getEvaluationsHistoryDetail(this.id)
      .then(res => {
        this.evaluation = res.data.data
        this.imagesList = this.filterImageSrc(this.evaluation.treasure.data.images.data)
      })
    },
    saveCtrl () {
      let verify = V.evaluations(this.eva)

      if (verify.status) {
        this.$vux.loading.show({
          text: '操作中...'
        })
        API.doneEvaluations(this.id, this.eva)
        .then(res => {
          this.$vux.toast.text('鉴定成功')
          this.$router.go(-1)
        })
      } else {
        this.$vux.toast.text(verify.text)
      }
    },
    filterImageSrc (data) {
      if (data.length !== 0) {
        return data.map(v => {
          v.src = v.url
          v.w = this.getImageWidth(v).width
          v.h = this.getImageWidth(v).height
          return v
        })
      }
    },
    getImageWidth (img) {
      let image = new Image()
      image.src = img.src
      let w = image.width
      let h = image.height
      return {
        width: w,
        height: h
      }
    }
  }
}



