
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
  data () {
    return {
      eva: this.$store.state.eva.eva || {}
    }
  },
  components: {
    XHeader
  },
  created () {
  },
  methods: {
    saveCtrl () {
      this.$store.commit('evaIncrement', this.eva)
      this.$router.go(-1)
    }
  }
}



