
import XHeader from 'vux/src/components/x-header/index.vue'

import API from '@/api'
import simulationScrollY from '@/components/common/scroll/simulation-scroll-y'

export default {
  data () {
    return {
      treasures: [],
      page: {
        page: 1,
        page_size: 5
      },
      loadMore: {},
      isMore: false
    }
  },
  components: {
    XHeader,
    simulationScrollY
  },
  created () {
    this.getMyTreasure()
    this.loadMoreFuc()
  },
  methods: {
    // 上传宝贝
    uploadCtrl () {
      this.$router.push({name: 'Upload'})
    },
    // 获取我的宝贝数据
    getMyTreasure (page) {
      if (this.page.page !== 1) {
        this.isMore = true
      } else {
        this.$vux.loading.show({
          text: '加载中...'
        })
      }
      API.getMyTreasure(page)
      .then(res => {
        if (res.data.length !== 0) {
          this._pageData(res.data)
          this.$nextTick(() => this.$refs.ssy.loadEnd())
          this.isMore = false
        }
      })
    },
    toDetail (treasure) {
      this.$router.push({name: 'Detail', query: {id: treasure.id}})
    },
    // 初始化数据
    _pageData (data) {
      this.treasures.push(...data.data)
      this.page.page = data.meta.pagination.current_page + 1
    },
    loadMoreFuc () {
      var that = this
      this.loadMore = {
        distance: 0,
        onLoadMore () {
          that.getMyTreasure(that.page)
        }
      }
    }
  }
}



