
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
  data () {
    return {
      expert: this.$store.state.expert.expert || {}
    }
  },
  components: {
    XHeader
  },
  created () {
  },
  methods: {
    saveCtrl () {
      this.$store.commit('expertIncrement', this.expert)
      this.$router.go(-1)
    }
  }
}



