
import XHeader from 'vux/src/components/x-header/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Group from 'vux/src/components/group/index.vue'

import API from '@/api'

export default {
  data () {
    return {
      expert: this.$store.state.expert.expert || {},
      cate: []
    }
  },
  components: {
    XHeader,
    Group,
    Checklist
  },
  created () {
    this.getCate()
  },
  methods: {
    getCate () {
      this.$vux.loading.show({
        text: '加载中...'
      })
      API.getCate('expert')
      .then(res => {
        this.cate = this.filterData(res.data.data)
      })
    },
    // 保存数据
    saveCtrl () {
      let key = this.expert.cate_key.sort((a, b) => a - b)
      this.expert.cate_value = this.filterCateValue(key)
      this.expert.category_ids = this.filterCateId(key)
      this.$store.commit('expertIncrement', this.expert)
      this.$router.go(-1)
    },
    // 过滤数据，添加value和key
    filterData (cate) {
      if (cate.length > 0) {
        cate.forEach((v, i) => {
          v.value = v.name
          v.key = i
        })
        return cate
      }
    },
    // 过滤添加cate的值
    filterCateValue (data) {
      let val = []
      if (data.length > 0) {
        data.forEach((v, i) => {
          if (i < 3) {
            val.push(this.cate[v].name)
          }
        })
      }
      return val.join()
    },
    // 过滤添加cate的id值
    filterCateId (data) {
      let val = []
      if (data.length > 0) {
        data.forEach((v, i) => {
          val.push(this.cate[v].id)
        })
      }
      return val.join()
    }
  }
}



